import { Lightning, Registry, Template } from '@lightningjs/sdk'
import { getElementRenderWidth } from '../utils'

export default class KeyDebug extends Lightning.Component {
    keyDebugFeatureFlag: boolean = KeyDebug.getKeyDebugFeatureFlag()

    private lastKeyCode = ''
    private consecutiveKeyPresses = 0
    private keyDebugDisplayTimeout?: number

    static _template(): Template {
        return {
            x: 150,
            y: 20,
            w: 300,
            h: 80,
            alpha: 0,
            Background: {
                w: (w: number) => w,
                h: (h: number) => h,
                color: 0xff05b34a,
                rect: true,
                shader: {
                    type: Lightning.shaders.RoundedRectangle,
                    radius: 15,
                },
            },
            KeyText: {
                x: 20,
                y: 15,
                text: {
                    fontFace: 'Regular',
                    fontSize: 35,
                },
            },
        }
    }

    captureKeyEvent(keyCode: string): boolean {
        if (!this.keyDebugFeatureFlag) {
            return false
        }

        if (this.keyDebugDisplayTimeout) {
            Registry.clearTimeout(this.keyDebugDisplayTimeout)
        }

        this.consecutiveKeyPresses =
            this.lastKeyCode === keyCode ? this.consecutiveKeyPresses + 1 : 0
        const keyCodeText = this.consecutiveKeyPresses
            ? keyCode + ' ' + (this.consecutiveKeyPresses + 1) + 'x'
            : keyCode

        this.patch({
            alpha: 1,
            KeyText: {
                text: {
                    text: keyCodeText,
                },
            },
        })
        const textWidth = 40 + getElementRenderWidth(this.tag('KeyText'))
        this.patch({ w: textWidth })
        this.keyDebugDisplayTimeout = Registry.setTimeout(() => {
            this.patch({ alpha: 0 })
            this.consecutiveKeyPresses = 0
            this.lastKeyCode = ''
        }, 1000)

        this.lastKeyCode = keyCode
        return false
    }

    static getKeyDebugFeatureFlag(): boolean {
        // using regex to get param, because window.location.search is empty string when param is added manually

        // get params from URL
        const rx1 = new RegExp('[?&](([^&=]+)=([^&=#]*))')
        const rx1match: Array<string> | null = window.location.href.match(rx1)
        if (rx1match && rx1match.length) {
            // get debug param from params
            const paramName = 'displayKeys'
            const rx2 = new RegExp('[?&]' + paramName + '=([^&]+).*$')
            const rx2match: Array<string> | null = rx1match[0].match(rx2)
            if (rx2match && rx2match.length >= 2) return rx2match[1].toLowerCase() === 'true'
            else return false
        } else return false
    }
}
