import RouterPage from './RouterPage'
import { PageDataError } from '../Errors'

export default class BasePage<T> extends RouterPage {
    private _data?: T

    get data(): T {
        if (this._data) {
            return this._data
        } else {
            throw new PageDataError()
        }
    }

    set data(value: T) {
        this._data = value
    }
}
