import PlatformInterface, {
    keyMapping,
    ToastMessageIdentifier,
    CCStatusCallback,
} from './PlatformInterface'
import { Log, Registry } from '@lightningjs/sdk'
import { TypedEmitter } from 'tiny-typed-emitter'

interface PlatformEvents {
    closedCaptionsStatusChanged: CCStatusCallback
}

const platformEvents = new TypedEmitter<PlatformEvents>()

export type DeviceType = 'browser' | 'X1'

export default class BasePlatform implements PlatformInterface {
    protected _screenHeight?: number
    protected _screenWidth?: number

    public async init(): Promise<void> {
        return
    }

    public shutdown(): void {
        Log.info('Shutdown event fallback')
    }

    public errorMetricsHandler(
        message: string,
        visible: boolean,
        code: string,
        args: Record<string, unknown> = {}
    ): void {
        Log.info('errorMetricsHandler event fallback', {
            message,
            visible,
            code,
            args,
        })
    }

    public dismissLoadingScreen(): void {
        Log.info('Dismiss loading screen event fallback')
    }

    public showToaster(messageIdentifier: ToastMessageIdentifier): void {
        Log.info('Showing Toaster fallback, with message: ', messageIdentifier)
    }

    public async getHDMIConnectedStatus(): Promise<boolean> {
        return true
    }

    public async getCloseCaptioningEnabled(): Promise<boolean> {
        return false
    }

    public get deviceType(): DeviceType {
        return 'browser'
    }

    public get screenHeight(): number {
        return window.innerHeight
    }

    public get screenWidth(): number {
        return window.innerWidth
    }

    public get hdmiConnectionRefreshInterval(): number {
        return 30000 // milliseconds
    }

    public appActionMetricsHandler(action: string, args: Record<string, unknown> = {}): void {
        Log.info('appActionMetricsHandler event fallback', {
            action,
            args,
        })
    }

    public get keyMapping(): keyMapping {
        return {
            80: 'MediaPlayPause',
            70: 'MediaFastForward',
            82: 'MediaRewind',
        }
    }

    private _ccEnabled?: boolean
    private _ccInterval?: number

    protected async _checkCCStatus(): Promise<void> {
        const ccEnabled = await this.getCloseCaptioningEnabled()
        if (ccEnabled !== this._ccEnabled) {
            this._ccEnabled = ccEnabled
            platformEvents.emit('closedCaptionsStatusChanged', ccEnabled)
        }
    }

    registerClosedCaptionsStatusListener(cb: CCStatusCallback): void {
        platformEvents.on('closedCaptionsStatusChanged', cb)
        Registry.clearInterval(this._ccInterval)
        this._ccInterval = Registry.setInterval(this._checkCCStatus.bind(this), 10000)
    }

    deregisterClosedCaptionsStatusListener(cb: CCStatusCallback): void {
        platformEvents.off('closedCaptionsStatusChanged', cb)
        Registry.clearInterval(this._ccInterval)
    }
}
