import { Template, Log, Router, Utils } from '@lightningjs/sdk'
import { BrandName } from '../Data/Models'
import BasePage from './BasePage'
import AppData from '../Data/Data'
import Store, { QsParams } from '../Store'

export default class Splash extends BasePage<AppData> {
    static _template(): Template {
        return {
            Image: {
                w: 1920,
                h: 1080,
                src: Utils.asset('images/splash_fhd.jpg'), // TODO: Change for animated background
            },
        }
    }

    _onDataProvided(): void {
        const params = Store.qs

        const usPrivacy = params.us_privacy
        if (usPrivacy) {
            this.handlePrivacy(usPrivacy)
        }

        const lmt = params.lmt
        if (lmt) {
            this.handleLimitedAdTracking(lmt)
        }

        const launchpoint = params.launchpoint
        if (launchpoint) {
            this.handleLaunchpoint(launchpoint, params)
        } else {
            this._goToHome()
        }
    }

    private _goToHome(): void {
        // TODO: Get brand by some other parameter?
        Router.navigate('brand/' + BrandName.nbcnews)
    }

    private async handleLaunchpoint(launchpoint: string, params: QsParams): Promise<void> {
        Log.info('Deeplink: Handling launchpoint params', launchpoint, params)
        switch (launchpoint) {
            case 'home': {
                this._goToHome()
                return
            }
            case 'detail': {
                const entityId = params.entityId
                if (entityId) {
                    this.handleEntityId(entityId)
                }
                return
            }
            case 'playback': {
                const assetId = params.assetId
                if (assetId) {
                    Router.navigate(`video/${encodeURIComponent(assetId)}`)
                }
                return
            }
            case 'search': {
                const query = params.query
                if (query) {
                    Router.navigate('search', { query: query })
                }
                return
            }
            case 'section': {
                const sectionName = params.sectionName
                if (sectionName) {
                    this.handleSection(sectionName)
                }
                return
            }
            default: {
                this._goToHome()
                return
            }
        }
    }

    private handlePrivacy(privacySetting: string): void {
        Log.info('Deeplink: Handling privacy setting', privacySetting)
        // TODO
        return
    }

    private handleLimitedAdTracking(iabSetting: string): void {
        Log.info('Deeplink: Handling limited ad tracking setting', iabSetting)
        // TODO
        return
    }

    private async handleEntityId(entityId: string) {
        const resource = await this.data.fetchResource(entityId)
        if ('type' in resource && resource.type === 'show') {
            Router.navigate(`show/${encodeURIComponent(entityId)}`)
        } else {
            Router.navigate(`playlist/${encodeURIComponent(entityId)}`)
        }
    }

    private handleSection(section: string) {
        // TODO: Support more sections like Shows, Watch
        switch (section) {
            case 'settings':
                Router.navigate('settings')
                return
            case 'search':
                Router.navigate('search')
                return
            default:
                return
        }
    }
}
