import BaseRow from './BaseRow'
import PlaylistsCell from '../cells/PlaylistsCell'
import { Cards, Card } from '../../Data/Models'

export default class PlaylistsRow extends BaseRow {
    protected _getCells(): { type: typeof PlaylistsCell; data: Card }[] {
        return (this.data as Cards).cards.map((card: Card) => ({
            type: PlaylistsCell,
            data: card,
        }))
    }
}
