import { Template, Router, Lightning } from '@lightningjs/sdk'
import { Video } from '../Data/Models'
import Events from '../Events'
import BasePage from './BasePage'
import PlayerUI from '../PlayerUI'
import Store from '../Store'
import PlayerMetrics from '../Analytics/PlayerMetrics'

export default class VideoPage extends BasePage<Video> {
    static _template(): Template {
        // Displays the loading + metadata on app load
        return {
            PlayerUI: {
                type: PlayerUI,
            },
        }
    }

    _onDataProvided(): void {
        Store.flags.playingDeeplinkedVideo = true
        const params = Store.qs
        const resumeTime = params.resumeTime ? Number(params.resumeTime) : 0
        Events.emit('$app.videoSelected', this.data, resumeTime)
        PlayerMetrics.start()
        Router.navigate('brand/' + this.data.brand, {
            hiddenOnLoad: true,
            skipAutoPlay: true,
        })
    }

    _getFocused(): Lightning.Element {
        return this.tag('PlayerUI')
    }
}
