import { FunctionKeyData } from '../../../Data/Models.js'
import KeyGroup from './KeyGroup.js'
import FunctionKey from '../Keys/FunctionKey.js'

export default class FunctionKeyGroup extends KeyGroup {
    keyWidth = 270

    set functions(values: FunctionKeyData[]) {
        const children = values.map((data: FunctionKeyData, i: number) => {
            return {
                x: this.keyWidth * i,
                w: this.keyWidth - this.keySpacing * 2,
                h: this.keyHeight - this.keySpacing * 2,
                type: FunctionKey,
                data: data,
                backgroundColor: this.keyBackgroundColor,
                passSignals: {
                    onFunctionKeyPressed: true,
                },
            }
        })

        this.patch({ children })
    }
}
