import { Lightning, Template } from '@lightningjs/sdk'

export default class Key extends Lightning.Component {
    static _template(): Template {
        return {
            Background: {
                w: (w: number) => w,
                h: (h: number) => h,
                rect: true,
            },
            Label: {
                x: (x: number) => x / 2,
                y: (y: number) => y / 2,
                zIndex: 1,
                mount: 0.5,
                text: {
                    fontSize: 30,
                },
            },
        }
    }

    _init(): void {
        this.focused = false
    }

    _focus(): void {
        this.focused = true
    }

    _unfocus(): void {
        this.focused = false
    }

    set focused(value: boolean) {
        this.patch({
            Background: {
                smooth: { alpha: value ? 1 : 0.5 },
            },
            Label: {
                smooth: {
                    color: value ? 0xff000000 : 0xffffffff,
                },
            },
        })
    }

    protected get labelComponent(): Lightning.Element {
        return this.tag('Label')
    }

    set label(value: string) {
        if (this.labelComponent.text) {
            this.labelComponent.text.text = value
        }
    }

    get label(): string {
        return this.labelComponent.text ? this.labelComponent.text.text : ''
    }

    set backgroundColor(value: number) {
        this.tag('Background').patch({ color: value })
    }
}
