import { Lightning, Registry, VideoPlayer } from '@lightningjs/sdk'
import HLSPlayer from './HLSPlayer'
import { Video } from '../Data/Models'
import Events, { PlayerEvent } from '../Events'
import Platform from '../Platform'

export default class Player extends Lightning.Component {
    private _player: HLSPlayer = new HLSPlayer(
        {
            application: 'nbcnews',
            platform: 'roku',
            requestorId: 'nbcnews',
        },
        this.$playerEvent.bind(this)
    )

    async _checkHdmiConnectionStatus(): Promise<void> {
        const connected = await Platform.getHDMIConnectedStatus()
        if (!connected && VideoPlayer.isPlaying) {
            VideoPlayer.pause()
        }
    }

    _init(): void {
        Events.on('$app.videoSelected', this.$videoSelected.bind(this))
        Registry.setInterval(
            this._checkHdmiConnectionStatus,
            Platform.hdmiConnectionRefreshInterval
        )
    }

    $playerEvent(ev: PlayerEvent): void {
        Events.emit('$player.playerEvent', ev)
    }

    $videoSelected(video: Video, resumeTime?: number): void {
        if (video.videoType === 'liveshow' && video.guid) {
            this._player.playWithPid(video.guid, resumeTime)
        } else if (video.videoUrl) {
            this._player.playWithUrl(video.videoUrl, resumeTime)
        }
    }
}
