import { Lightning } from '@lightningjs/sdk'

const IMG_REGEX = /(.+)(\/i\/)(.+\.)(.+)$/
const REPLACE_PATTERN = '$1/j/$3{w};{h};7;80;5.$4'

export function NBCDAimsUrl(baseUrl: string, width: number, heigth: number): string {
    if (IMG_REGEX.test(baseUrl)) {
        return baseUrl
            .replace(IMG_REGEX, REPLACE_PATTERN)
            .replace('{w}', `${width}`)
            .replace('{h}', `${heigth}`)
    } else {
        return baseUrl
    }
}

export function clamp(value: number, min: number, max: number): number {
    return Math.max(min, Math.min(value, max))
}

export function clamp01(value: number): number {
    return clamp(value, 0, 1)
}

export function formatPublishedDate(value: Date | string): string {
    const date = new Date(value)
    const minutesAgo = (Date.now() - date.getTime()) / (1000 * 60)
    const hoursAgo = minutesAgo / 60

    let text = ''

    if (minutesAgo < 60) {
        text = `${Math.floor(minutesAgo)}m ago`
    } else if (hoursAgo < 24) {
        text = `${Math.floor(hoursAgo)}h ago`
    } else {
        text = date.toLocaleString('en-us', { month: 'short' })
        text += ' '
        text += date.getDate()
    }

    return text
}

export function formatDuration(value: string): string {
    const time = value.split(':').map((val: string) => parseInt(val))
    const timeUnit = time[0] > 0 ? 'hr' : time[1] > 0 ? 'min' : 'sec'
    return `${value.replace(/^0(?:0:0?)?/, '')} ${timeUnit}`
}

/**
 * The prototype of a function returning width of text element given its style.
 *
 * @param tag tag of element to get render width from
 * @param context component of which is element part of
 */

type ElementWithTexture = Lightning.Element & { loadTexture?: () => void } & {
    renderWidth?: number
}
export function getElementRenderWidth(element: Lightning.Element): number {
    const e: ElementWithTexture = element
    if (e.loadTexture) {
        e.loadTexture()
    }
    return e.renderWidth || 0
}
