import { BaseCell } from '../components/cells'
import BaseRow from '../components/rows/BaseRow'
import { Video, Card, Channel } from '../Data/Models'

export class CellSelectedPayload {
    row: BaseRow
    cell: BaseCell

    constructor(row: BaseRow, cell: BaseCell) {
        this.row = row
        this.cell = cell
    }
}

export interface CellData {
    type: typeof BaseCell
    data: Video | Card
}

export interface ChannelRow {
    type: typeof BaseRow
    data: Channel
    titleVisible: boolean
}
