import { Lightning, Router, Template, States } from '@lightningjs/sdk'
import Button from '../components/Button'
import Platform from '../Platform'

export default class ExitModal extends Lightning.Component {
    static _template(): Template {
        const width = 1920
        const height = 1080
        const boxWidth = 580
        const boxHeight = 120
        const buttonWidth = 174
        const buttonHeight = 61
        return {
            alpha: 0,
            Overlay: {
                w: width,
                h: height,
                rect: true,
                color: 0xe0000000,
            },
            Box: {
                x: width / 2 - boxWidth / 2,
                y: height / 2 - boxHeight / 2,
                w: boxWidth,
                h: boxHeight,
                texture: Lightning.Tools.getRoundRect(
                    boxWidth,
                    boxHeight,
                    10,
                    2,
                    0xffffffff,
                    true,
                    0xe6000000
                ),
                Continue: {
                    x: 80,
                    y: boxHeight / 2 - buttonHeight / 2,
                    type: Button,
                    buttonText: 'Continue',
                },
                Quit: {
                    x: buttonWidth + 140,
                    y: boxHeight / 2 - buttonHeight / 2,
                    type: Button,
                    buttonText: 'Quit App',
                },
            },
        }
    }

    static _states(): States {
        return [
            class ContinueFocused extends this {
                _getFocused() {
                    return this.tag('Continue')
                }
                _handleEnter() {
                    Router.focusPage()
                }
                _handleRight() {
                    this._setState('QuitFocused')
                }
                _handleLeft(): boolean {
                    return true
                }
                _handleBack() {
                    Router.focusPage()
                }
            },
            class QuitFocused extends this {
                _getFocused() {
                    return this.tag('Quit')
                }
                _handleRight(): boolean {
                    return true
                }
                _handleLeft() {
                    this._setState('ContinueFocused')
                }
                _handleBack() {
                    Router.focusPage()
                }
                _handleEnter() {
                    Platform.shutdown()
                    this.application.closeApp()
                }
            },
        ]
    }

    _focus(): void {
        // this.patch({ alpha: 1 })
        this.setSmooth('alpha', 1)
        this._setState('ContinueFocused')
    }

    _unfocus(): void {
        // this.patch({ alpha: 0 })
        this.setSmooth('alpha', 0)
    }
}
