import { Template } from '@lightningjs/sdk'
import BaseCell from './BaseCell'
import { NBCDAimsUrl } from '../../utils'

export default class LiveCell extends BaseCell {
    private static width = 386
    private static height = 140

    static _template(): Template {
        return {
            ...LiveCell._getBaseTemplate(LiveCell.width, LiveCell.height),
        }
    }

    protected _render(): void {
        if ('tease' in this.data) {
            this.image.src = NBCDAimsUrl(this.data.tease, LiveCell.width, LiveCell.height)
        }
    }
}
