import { Lightning, Router, Template, Utils } from '@lightningjs/sdk'
import { BrandName } from '../../Data/Models'
import { clamp } from '../../utils'
import TabMenuItem from './TabMenuItem'
import Events from '../../Events'

export default class TabMenu extends Lightning.Component {
    private _index = 0

    static _template(): Template {
        const barHeight = 72
        const padding = 28 * 2

        const widths = {
            news: 146 + padding,
            msnbc: 163 + padding,
            today: 161 + padding,
            settings: 24 + padding,
            search: 24 + padding,
        }

        const roundedRectangleShader = {
            type: Lightning.shaders.RoundedRectangle,
            radius: 10,
        }

        return {
            x: 1920 / 2,
            y: 46,
            mountX: 0.5,
            h: barHeight,
            Background: {
                w: (w: number) => w,
                h: (h: number) => h,
                rect: true,
                color: 0xc2000000,
                shader: roundedRectangleShader,
            },
            FocusedIndicator: {
                h: (h: number) => h,
                x: 0,
                alpha: 0,
                rect: true,
                color: 0xffffffff,
                shader: roundedRectangleShader,
            },
            Items: {
                News: {
                    w: widths.news,
                    h: barHeight,
                    type: TabMenuItem,
                    image: Utils.asset('images/top-bar/tab_news_light.png'),
                    onSelected: () => Router.navigate('brand/' + BrandName.nbcnews),
                },
                Msnbc: {
                    w: widths.msnbc,
                    h: barHeight,
                    type: TabMenuItem,
                    image: Utils.asset('images/top-bar/tab_msnbc_light.png'),
                    onSelected: () => Router.navigate('brand/' + BrandName.msnbc),
                },
                Today: {
                    w: widths.today,
                    h: barHeight,
                    type: TabMenuItem,
                    image: Utils.asset('images/top-bar/tab_today_light.png'),
                    onSelected: () => Router.navigate('brand/' + BrandName.today),
                },
                Search: {
                    w: widths.search,
                    h: barHeight,
                    type: TabMenuItem,
                    image: Utils.asset('images/top-bar/tab_search_light.png'),
                    onSelected: () => Router.navigate('search'),
                },
                Settings: {
                    w: widths.settings,
                    h: barHeight,
                    type: TabMenuItem,
                    image: Utils.asset('images/top-bar/tab_settings_light.png'),
                    onSelected: () => Router.navigate('settings'),
                },
            },
        }
    }

    //
    // component's events
    _init(): void {
        this._initWidth()
        Events.on('$app.firstRoute', this._setActiveItemFromHash.bind(this))
    }

    private _focus(): void {
        this.focusedIndicator.setSmooth('alpha', 1)
        this._updateFocusedIndicator(false)
    }

    private _unfocus(): void {
        this.focusedIndicator.setSmooth('alpha', 0)
    }

    _getFocused(): Lightning.Element {
        return this.selected
    }

    private _handleRight(): void {
        this.index++
    }

    private _handleLeft(): void {
        this.index--
    }

    private _handleEnter(): void {
        this.selected.onSelected()
        this._updateActiveItemColor()
    }

    _handleDown(): void {
        Router.focusPage()
    }

    _handleUp(): boolean {
        // This prevents a bug where focus goes out of the widget on Up
        return true
    }

    _handleBack(): void {
        Router.focusWidget('ExitModal')
    }

    show(): void {
        this.setSmooth('alpha', 1)
    }

    hide(): void {
        this.setSmooth('alpha', 0)
    }

    private _updateActiveItemColor() {
        this.items.forEach((item: TabMenuItem, i: number) => {
            item.useActiveColor = i === this.index
        })
    }

    private _setActiveItemFromHash(hash: string): void {
        const tabItems = [
            `brand/${BrandName.nbcnews}`,
            `brand/${BrandName.msnbc}`,
            `brand/${BrandName.today}`,
            'search',
            'settings',
        ]
        const index = tabItems.indexOf(hash)
        if (index > -1) {
            this.index = index
            this._updateActiveItemColor()
        }
    }

    private _initWidth(): void {
        let width = 0

        this.items.forEach(item => {
            item.x = width
            width += item.w
        })

        this.w = width
    }

    private _updateFocusedIndicator(smooth = true) {
        const values = {
            w: this.selected.w,
            x: this.selected.x,
        }

        const patch = smooth ? { smooth: values } : { ...values }

        this.focusedIndicator.patch(patch)
    }

    //
    // properties
    private get focusedIndicator(): Lightning.Element {
        return this.tag('FocusedIndicator')
    }

    private get items(): TabMenuItem[] {
        return this.tag('Items').children as TabMenuItem[]
    }

    private get selected(): TabMenuItem {
        return this.items[this.index] as TabMenuItem
    }

    private get index(): number {
        return this._index
    }

    private set index(value: number) {
        this._index = clamp(value, 0, this.items.length - 1)
        this._updateFocusedIndicator()
    }
}
