import BaseRow from './BaseRow'
import { Video, Playlist } from '../../Data/Models'
import LiveCell from '../cells/LiveCell'

export default class LiveRow extends BaseRow {
    protected _getCells(): { type: typeof LiveCell; data: Video }[] {
        return (this.data as Playlist).videos.map((vid: Video) => ({
            type: LiveCell,
            data: vid,
        }))
    }

    _init(): void {
        super._init()
        this.tag('Header.Counter').patch({ alpha: 0 })
    }
}
