import { Template } from '@lightningjs/sdk'
import { FunctionKeyData } from '../../../Data/Models'
import Key from './Key'

export default class FunctionKey extends Key {
    private _data?: FunctionKeyData

    static _template(): Template {
        return {
            ...super._template(),
            Icon: {
                x: (x: number) => x / 2,
                y: (y: number) => y / 2,
                zIndex: 1,
                mount: 0.5,
                visible: false,
            },
        }
    }
    set data(value: FunctionKeyData) {
        this._data = value
        this.label = value.label

        if (value.src) {
            this.patch({
                Label: {
                    visible: false,
                },
                Icon: {
                    src: value.src,
                    visible: true,
                },
            })
        }
    }

    set focused(value: boolean) {
        this.patch({
            Background: {
                smooth: { alpha: value ? 1 : 0.5 },
            },
            Label: {
                smooth: {
                    color: value ? 0xff000000 : 0xffffffff,
                },
            },
            Icon: {
                smooth: {
                    color: value ? 0xff000000 : 0xffffffff,
                },
            },
        })
    }

    _handleEnter(): void {
        if (this._data) {
            this.signal('onFunctionKeyPressed', this._data.eventName)
        }
    }
}
