import { Log } from '@lightningjs/sdk'
import { ConfigNotReadyError, FetchError } from './Errors'
import { Settings } from '@lightningjs/sdk'
import analytics from './Analytics/Analytics'

export default class Config {
    /* eslint-disable @typescript-eslint/no-explicit-any*/
    [index: string]: any

    private _ready: boolean
    private _configUrl: string
    private _config?: ConfigData

    constructor(env: string) {
        this._configUrl = this.configUrl(env)
        this._ready = false

        return new Proxy(this, {
            get(target, prop) {
                if (prop in target) {
                    return target[prop as keyof Config]
                }

                if (target._ready && target._config) {
                    return target._config[prop as keyof ConfigData]
                } else {
                    throw new ConfigNotReadyError()
                }
            },
        })
    }

    async fetch(): Promise<void> {
        const result = await fetch(this._configUrl, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
            },
            mode: 'cors',
        })

        if (result.ok) {
            const root = await result.json()
            // TD: check agains schema for validity
            this._config = root
            this._ready = true
            return
        } else {
            Log.info(result.statusText)
            analytics.error('ServerError', {
                id: 'config',
                url: result.url,
                code: result.status,
                description: result.statusText,
            })
            throw new FetchError(result.statusText, result.status, result.url)
        }
    }

    private configUrl(env: string): string {
        const pid = Settings.get('app', 'provisioning')
        const version = Settings.get('app', 'version')
        return `https://deviceservices.nbcnews.com/config/ott/${pid}/${env}/${version}`
    }
}

interface StringStringDictionary {
    [index: string]: string
}

interface ConfigData {
    urls: StringStringDictionary
    brands: StringStringDictionary
    playmakeradsconfig: {
        baseURL: string
        z: string
        g: string
        u: string
        ptcueformat: string
        pttoken: string
        caid: string
    }
    freewheel: {
        adTag: string
    }
}
