import { Lightning, Template } from '@lightningjs/sdk'
import { Row } from 'lightning-ui-components'
import { Channel } from '../../Data/Models'
import { CellSelectedPayload, CellData } from '../../ViewModels/ViewModels'
import { RowDataError } from '../../Errors'
import Events from '../../Events'
import { clamp } from '../../utils'
import Store from '../../Store'
import { BaseCell } from '../cells'

export default abstract class BaseRow extends Lightning.Component {
    private _data?: Channel
    private static readonly headerHeight = 46
    private static readonly headerMargin = 23

    static _template(): Template {
        const width = 1920
        const xOffset = 22
        const leftMargin = 108
        const counterRightMargin = 281

        const text = {
            text: {
                fontFace: 'FoundersGroteskCondensed-Semibold',
                fontSize: 46,
                textColor: 0xffffffff,
                cutSy: 16,
            },
        }

        return {
            x: leftMargin,
            w: width - leftMargin,
            clipping: true,
            Header: {
                x: xOffset,
                h: BaseRow.headerHeight,
                Title: {
                    ...text,
                },
                Counter: {
                    ...text,
                    x: width - counterRightMargin,
                    mountX: 1,
                },
            },
            Row: {
                x: xOffset,
                type: Row,
                itemSpacing: 40,
                wrapSelected: true,
                signals: {
                    selectedChange: '_selectedChangeHandler',
                },
            },
        }
    }

    //
    // override
    protected abstract _getCells(): CellData[]

    //
    // callbacks
    _init(): void {
        this._focused = false
        Events.on('$app.videoSelected', this.$videoSelected.bind(this))
    }

    _detach(): void {
        Events.off('$app.videoSelected', this.$videoSelected)
    }

    _focus(): void {
        this._focused = true
    }

    _unfocus(): void {
        this._focused = false
    }

    _getFocused(): Lightning.Element {
        return this.row
    }

    _active(): void {
        this._updateNowPlayingIcon()
    }

    //
    // helpers
    private _selectedChangeHandler() {
        this._updateCounterText()
    }

    private _updateCounterText() {
        this.header.patch({
            Counter: {
                text: `${this.selectedIndex + 1} of ${this.row.items.length}`,
            },
        })
    }

    private _cellSelected(): void {
        Events.emit('$app.cellSelected', new CellSelectedPayload(this, this.row.selected))
    }

    private _updateHeight() {
        const padding = 10

        if (this.header.visible) {
            this.row.y = BaseRow.headerHeight + BaseRow.headerMargin
            this.h = this.row.h + BaseRow.headerHeight + BaseRow.headerMargin + padding
        } else {
            this.row.y = 10
            this.h = this.row.h + padding * 2
        }
    }

    private _updateNowPlayingIcon(): void {
        if (Store.isReady) {
            // In case we come from a deeplink, store won't be ready yet
            this.row.items.forEach((item: BaseCell) => {
                item.nowPlaying = false
            })
            if (Store.selectedChannel.id === this.data.id) {
                const selectedItem: BaseCell = this.row.items[Store.selectedGrid.position.x]
                selectedItem.nowPlaying = true
            }
        }
    }

    //
    // properties
    private set _focused(value: boolean) {
        const textColor = value ? 0xffffffff : 0xff898989

        this.header.patch({
            Title: { text: { textColor: textColor } },
            Counter: { text: { textColor: textColor } },
        })
    }

    set data(value: Channel) {
        this._data = value
        this.title.patch({ text: value.title.toUpperCase() })
        this.row.items = this._getCells()

        this._updateHeight()
        this._updateCounterText()
    }

    get data(): Channel {
        if (this._data) {
            return this._data
        } else {
            throw new RowDataError()
        }
    }

    set titleVisible(value: boolean) {
        this.header.patch({ visible: value })
        this._updateHeight()
    }

    private get header() {
        return this.tag('Header')
    }

    private get title() {
        return this.tag('Title')
    }

    get titleText(): string {
        return this.title.text ? this.title.text.text : ''
    }

    set titleText(value: string) {
        this.title.patch({ text: value })
    }

    private get row() {
        return this.tag('Row') as Row
    }

    get selectedIndex(): number {
        return this.row.selectedIndex
    }

    set selectedIndex(value: number) {
        this.row.selectedIndex = clamp(value, 0, this.row.items.length - 1)
    }

    //
    // Input
    _handleEnter(): void {
        this._cellSelected()
    }

    async $videoSelected(): Promise<void> {
        this._updateNowPlayingIcon()
    }
}
