import { Lightning, Template, Utils, States, Animation } from '@lightningjs/sdk'

export default class LoadingIndicator extends Lightning.Component {
    private _rotateAnim?: Animation
    private _sweepOnAnim?: Animation
    private _sweepOffAnim?: Animation
    private _feathersSweepAnimation = {
        items: ['F1', 'F2', 'F3', 'F4', 'F5', 'F6'],
        delay: 0.4,
        duration: 0.8,
        fadeDuration: 0.2,
        startValue: 0.5,
        endValue: 1,
    }

    static _template(): Template {
        return {
            Feathers: {
                w: 225.32,
                h: 134.58,
                mount: 0.5,
                F1: {
                    x: 0,
                    y: 86.58,
                    w: 99,
                    h: 48,
                    pivot: 0.5,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather1.png'),
                },
                F2: {
                    x: 17.23,
                    y: 32.26,
                    w: 80,
                    h: 92,
                    pivot: 0.5,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather2.png'),
                },
                F3: {
                    x: 62.13,
                    y: 0,
                    w: 48,
                    h: 108,
                    pivotX: 18,
                    pivotY: 18,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather3.png'),
                },
                F4: {
                    x: 115.91,
                    y: 0,
                    w: 48,
                    h: 108,
                    pivotX: 18,
                    pivotY: 18,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather4.png'),
                },
                F5: {
                    x: 128.43,
                    y: 32.54,
                    w: 80,
                    h: 92,
                    pivot: 0.5,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather5.png'),
                },
                F6: {
                    x: 126.32,
                    y: 86.58,
                    w: 99,
                    h: 48,
                    pivot: 0.5,
                    alpha: 0.5,
                    src: Utils.asset('images/loading-indicator/Feather6.png'),
                },
            },
        }
    }

    _init(): void {
        this._rotateAnim = this.tag('Feathers').animation({
            duration: 0.5,
            actions: [
                {
                    t: 'F1',
                    p: 'rotation',
                    v: { 0: -0.8, 0.9: { v: 0.05, sm: 0.8 }, 1: { v: 0, sm: 1 } },
                    rv: 0,
                },
                {
                    t: 'F2',
                    p: 'rotation',
                    v: { 0: -0.4, 0.9: { v: 0.02, sm: 0.8 }, 1: { v: 0, sm: 1 } },
                    rv: 0,
                },
                {
                    t: 'F5',
                    p: 'rotation',
                    v: { 0: 0.4, 0.9: { v: -0.02, sm: 0.8 }, 1: { v: 0, sm: 1 } },
                    rv: 0,
                },
                {
                    t: 'F6',
                    p: 'rotation',
                    v: { 0: 0.8, 0.9: { v: -0.05, sm: 0.8 }, 1: { v: 0, sm: 1 } },
                    rv: 0,
                },
            ],
        })
        const { startValue, endValue } = this._feathersSweepAnimation

        this._sweepOnAnim = this.tag('Feathers').animation(
            this.getSweepAnimation(startValue, endValue)
        )

        this._sweepOffAnim = this.tag('Feathers').animation(
            this.getSweepAnimation(endValue, startValue)
        )

        if (this._rotateAnim.on) {
            this._rotateAnim.on('finish', () => {
                this._sweepOnAnim && this._sweepOnAnim.start()
            })
        }

        if (this._sweepOnAnim.on) {
            this._sweepOnAnim.on('finish', () => {
                this._sweepOffAnim && this._sweepOffAnim.start()
            })
        }

        if (this._sweepOffAnim.on) {
            this._sweepOffAnim.on('finish', () => {
                this._sweepOnAnim && this._sweepOnAnim.start()
            })
        }
    }

    static _states(): States {
        return [
            class Run extends this {
                $enter() {
                    this.start()
                }
                $exit() {
                    this.clear()
                }
            },
            class Stop extends this {
                $enter() {
                    this.stop()
                }
                $exit() {
                    this.clear()
                }
            },
        ]
    }

    _active(): void {
        this._setState('Run')
    }

    _inactive(): void {
        this._setState('Stop')
    }

    private start(): void {
        this.clear()
        this._rotateAnim && this._rotateAnim.start()
    }

    private stop(): void {
        this._rotateAnim && this._rotateAnim.pause()
        this._sweepOnAnim && this._sweepOnAnim.pause()
        this._sweepOffAnim && this._sweepOffAnim.pause()
    }

    private clear(): void {
        this.stop()
        const initialValues = { alpha: 0.5, rotation: 0 }
        this.patch({
            Feathers: {
                F1: initialValues,
                F2: initialValues,
                F3: initialValues,
                F4: initialValues,
                F5: initialValues,
                F6: initialValues,
            },
        })
    }

    private getSweepAnimation(startValue: number, endValue: number): Record<string, unknown> {
        const { items, delay, duration, fadeDuration } = this._feathersSweepAnimation
        const step = duration / items.length
        const startTimes = [1, 2, 3, 4, 5, 6].map(n => n * step)
        const endTimes = startTimes.map(n => n + fadeDuration)
        return {
            delay: delay,
            duration: duration,
            actions: items.map((item: string, i: number) => {
                return {
                    t: item,
                    p: 'alpha',
                    v: {
                        0: startValue,
                        [startTimes[i]]: startValue,
                        [endTimes[i]]: endValue,
                        1: endValue,
                    },
                }
            }),
        }
    }
}
