import { Lightning, VideoPlayer, Utils, Template, States } from '@lightningjs/sdk'
import { clamp01, clamp } from '../utils'
import Timer from '../components/Timer'
import Loading from './Loading'
import Buffering from './Buffering'
import Bumper from './Bumper'
import { Video } from '../Data/Models'
import Events, { PlayerEvent } from '../Events'
import Store from '../Store'

const SECONDS_TO_FIRE_BUMPER = 4
const BAR_WIDTH = 1250
const BAR = {
    w: BAR_WIDTH,
    h: 50,
    x: (1920 - BAR_WIDTH) / 2,
    y: 855,
    backgroundColor: 0xff162d61,
    color: 0xff995896,
}
export default class PlayerControls extends Lightning.Component {
    _focusAnimDuration = 0.5
    _hideTimer: Timer = new Timer(this, 5, () => this._setState('Hidden'))
    _seekValue = 0
    _bumperFired = false

    static _template(): Template {
        return {
            Controls: {
                x: 0,
                y: 1080,
                Background: {
                    x: 0,
                    y: 810,
                    w: 1920,
                    h: 270,
                    rect: true,
                    color: 0xff102039,
                },
                ProgressBar: {
                    x: BAR.x,
                    y: BAR.y,
                    Background: {
                        texture: Lightning.Tools.getRoundRect(
                            BAR.w,
                            BAR.h,
                            5,
                            0,
                            0,
                            true,
                            BAR.backgroundColor
                        ),
                    },
                    Progress: {
                        w: BAR.w,
                        texture: Lightning.Tools.getRoundRect(
                            BAR.w,
                            BAR.h,
                            5,
                            0,
                            0,
                            true,
                            BAR.color
                        ),
                    },
                },
                Time: {
                    y: BAR.y + 10,
                    Current: {
                        x: BAR.x - 20,
                        mountX: 1,
                        text: {
                            fontFace: 'Regular',
                            fontSize: 30,
                            textColor: 0xbbffffff,
                            textAlign: 'right',
                            text: '20s',
                        },
                    },
                    Remaining: {
                        x: 1920 - BAR.x + 20,
                        text: {
                            fontFace: 'Regular',
                            fontSize: 30,
                            textColor: 0xbbffffff,
                            text: '60s',
                        },
                    },
                },
                StatusIcon: {
                    x: 1920 / 2,
                    y: 970,
                    src: Utils.asset('images/player/pause.png'),
                },
            },
            Loading: {
                type: Loading,
                alpha: 0,
            },
            Buffering: {
                type: Buffering,
                alpha: 0,
            },
            Bumper: {
                type: Bumper,
            },
        }
    }

    static _states(): States {
        return [
            class Playing extends this {
                $enter() {
                    this.signal('playerState', 'Playing')
                    if (this._hideTimer) {
                        this._hideTimer.start()
                    }
                    this._setStateIcon('Pause')
                    VideoPlayer.play()
                }
                _handleMediaPlayPause() {
                    this._handleEnter()
                }
                _handleEnter() {
                    this._setState('Paused')
                }
                _handleMediaFastForward() {
                    this._onFF()
                }
                _handleRight(): void {
                    this._onFF()
                }
                _handleMediaRewind() {
                    this._onRW()
                }
                _handleLeft(): void {
                    this._onRW()
                }
                _handleBack(): void {
                    this._setState('Hidden')
                }
            },
            class Paused extends this {
                $enter() {
                    this.signal('playerState', 'Paused')
                    if (this._hideTimer) {
                        this._hideTimer.stop()
                    }
                    this._setStateIcon('Play')
                    VideoPlayer.pause()
                }
                _handleMediaPlayPause() {
                    this._handleEnter()
                }
                _handleEnter() {
                    this._setState('Playing')
                }
                _handleMediaFastForward() {
                    this._onFF()
                }
                _handleRight(): void {
                    this._onFF()
                }
                _handleMediaRewind() {
                    this._onRW()
                }
                _handleLeft(): void {
                    this._onRW()
                }
                _handleBack(): void {
                    this._setState('Hidden')
                }
            },
            class Seeking extends this {
                $enter() {
                    this.signal('playerState', 'Seeking')
                    if (this._hideTimer) {
                        this._hideTimer.stop()
                    }
                    VideoPlayer.pause()
                    this._seekValue = VideoPlayer.currentTime
                }
                _handleMediaPlayPause() {
                    this._handleEnter()
                }
                _handleEnter() {
                    VideoPlayer.seek(this._seekValue)
                    this._seekValue = 0
                }
                _handleMediaFastForward() {
                    this._onFF()
                }
                _handleRight(): void {
                    this._onFF()
                }
                _handleMediaRewind() {
                    this._onRW()
                }
                _handleLeft(): void {
                    this._onRW()
                }
                _handleBack(): void {
                    this._setState('Hidden')
                }
            },
            class Hidden extends this {
                $enter() {
                    this.controls.setSmooth('y', 1080, { duration: this._focusAnimDuration })
                }
                $exit() {
                    this.controls.setSmooth('y', 0, { duration: this._focusAnimDuration })
                }
                _handleMediaPlayPause() {
                    this._handleEnter()
                }
                _handleEnter() {
                    this._setState(VideoPlayer.isPlaying ? 'Paused' : 'Playing')
                }
                _handleMediaFastForward() {
                    this._onFF()
                }
                _handleRight(): void {
                    this._onFF()
                }
                _handleMediaRewind() {
                    this._onRW()
                }
                _handleLeft(): void {
                    this._onRW()
                }
            },
            class Buffering extends this {
                $enter() {
                    this.controls.setSmooth('alpha', 0, { duration: 0.5 })
                    this.buffering.setSmooth('alpha', 1, { duration: 0.5 })
                }
                $exit() {
                    this.controls.setSmooth('alpha', 1, { duration: 0.5 })
                    this.buffering.setSmooth('alpha', 0, { duration: 0.5 })
                }
            },
            class Loading extends this {
                $enter() {
                    this.controls.patch({ alpha: 0 })
                    this.loading.patch({ alpha: 1 })
                }
                $exit() {
                    this.controls.setSmooth('alpha', 1, { duration: 0.5 })
                    this.loading.setSmooth('alpha', 0, { duration: 0.5 })
                }
            },
        ]
    }

    //
    // component's events
    _init(): void {
        this._setState('Hidden')
        Events.on('$app.videoSelected', this.$videoSelected.bind(this))
        Events.on('$player.playerEvent', this.$playerEvent.bind(this))
    }

    _detach(): void {
        Events.off('$app.videoSelected', this.$videoSelected)
        Events.off('$player.playerEvent', this.$playerEvent)
    }

    _getFocused(): Lightning.Element {
        return this
    }

    _focus(): void {
        if (this._hideTimer && (this.state === 'Playing' || this.state === 'Paused')) {
            this._hideTimer.start()
        }
    }

    _captureKey(): boolean {
        this.signal('screenKey')
        if (this._hideTimer && (this.state === 'Playing' || this.state === 'Paused')) {
            this._hideTimer.start()
        }
        return false
    }

    $videoSelected(video: Video): void {
        this.loading.patch({ item: video })
        this._bumperFired = false
        this._seekValue = 0
    }

    $playerEvent({ type: eventType }: PlayerEvent): void {
        if (!this.active) {
            return
        }
        if (this.state === 'Hidden') {
            switch (eventType) {
                case 'LoadStart':
                    this._setState('Loading')
                    return
                case 'TimeUpdate': {
                    this._checkIfBumperNeeded()
                    break
                }
                case 'Waiting':
                    this._setState('Buffering')
                    break
                default:
                    break
            }
        } else if (this.state === 'Loading') {
            switch (eventType) {
                case 'Waiting':
                    this.loading.patch({ progress: 20 })
                    return
                case 'DurationChange':
                    this.loading.patch({ progress: 40 })
                    return
                case 'LoadedMetadata':
                    this.loading.patch({ progress: 60 })
                    return
                case 'LoadedData':
                    this.loading.patch({ progress: 80 })
                    return
                case 'CanPlay':
                    this.loading.patch({ progress: 100 })
                    return
                case 'Playing':
                    this._setState('Hidden')
                    return
                default:
                    break
            }
        } else if (this.state === 'Buffering') {
            switch (eventType) {
                case 'Playing':
                    this._setState('Hidden')
                    return
                default:
                    break
            }
        } else if (this.state === 'Seeking') {
            switch (eventType) {
                case 'Seeked':
                    this._setState('Playing')
                    return
                default:
                    break
            }
        } else if (this.state === 'Playing' || this.state === 'Paused') {
            switch (eventType) {
                case 'TimeUpdate': {
                    this._setProgressBarValue()
                    this._checkIfBumperNeeded()
                    break
                }
                case 'Waiting':
                    this._setState('Buffering')
                    break
                default:
                    break
            }
        }
    }

    _checkIfBumperNeeded(): void {
        if (!this._bumperFired) {
            const remainingTime = VideoPlayer.duration - VideoPlayer.currentTime
            if (remainingTime <= SECONDS_TO_FIRE_BUMPER) {
                this.bumper.patch({ item: Store.nextVideo })
                this._bumperFired = true
            }
        }
    }

    //
    // helpers
    _setProgressBarValue(
        currentTime = VideoPlayer.currentTime,
        duration = VideoPlayer.duration
    ): void {
        const durationScaled = clamp01(currentTime / Math.max(duration, 1))
        const progress = clamp(BAR_WIDTH * durationScaled, 10, BAR_WIDTH)
        const remainingTime = duration - currentTime
        const currentTimeText = this._formatTime(Math.floor(currentTime))
        const remainingTimeText = this._formatTime(Math.floor(remainingTime))

        this.progressIndicator.patch({ w: progress })

        const currentTimeTextTag = this.currentTime.text
        if (currentTimeTextTag) {
            currentTimeTextTag.text = currentTimeText
        }
        const remainingTimeTextTag = this.remainingTime.text
        if (remainingTimeTextTag) {
            remainingTimeTextTag.text = remainingTimeText
        }
    }

    _seek(value: number): void {
        this._setState('Seeking')
        this._setStateIcon(value > 0 ? 'SeekForward' : 'SeekBackward')
        this._seekValue = clamp(this._seekValue + value, 0, VideoPlayer.duration)
        this._setProgressBarValue(this._seekValue)
    }

    _setStateIcon(status: string): void {
        switch (status) {
            case 'Play':
                this.statusIcon.src = Utils.asset('images/player/play.png')
                break
            case 'Pause':
                this.statusIcon.src = Utils.asset('images/player/pause.png')
                break
            case 'SeekForward':
                this.statusIcon.src = Utils.asset('images/player/seek_forward.png')
                break
            case 'SeekBackward':
                this.statusIcon.src = Utils.asset('images/player/seek_backward.png')
                break
        }
    }

    _formatTime(time: number): string {
        const minutes = Math.floor(time / 60)
        const seconds = time - minutes * 60
        const minutesText = minutes > 0 && minutes !== Infinity ? `${minutes}m` : ''
        const secondsText = seconds !== Infinity ? `${seconds}s` : ''
        return minutesText + secondsText
    }

    _onFF(): void {
        this._seek(10)
    }

    _onRW(): void {
        this._seek(-10)
    }

    private get controls(): Lightning.Element {
        return this.tag('Controls')
    }

    private get buffering(): Lightning.Element {
        return this.tag('Buffering')
    }

    private get loading(): Lightning.Element {
        return this.tag('Loading')
    }

    private get bumper(): Lightning.Element {
        return this.tag('Bumper')
    }

    private get statusIcon(): Lightning.Element {
        return this.tag('Controls').tag('StatusIcon')
    }

    private get currentTime(): Lightning.Element {
        return this.tag('Controls')
            .tag('Time')
            .tag('Current')
    }

    private get remainingTime(): Lightning.Element {
        return this.tag('Controls')
            .tag('Time')
            .tag('Remaining')
    }

    private get progressIndicator(): Lightning.Element {
        return this.tag('Controls')
            .tag('ProgressBar')
            .tag('Progress')
    }
}
