import BaseRow from './BaseRow'
import VideosCell from '../cells/VideosCell'
import { Playlist, Video } from '../../Data/Models'

export default class VideosRow extends BaseRow {
    protected _getCells(): { type: typeof VideosCell; data: Video }[] {
        return (this.data as Playlist).videos.map((vid: Video) => ({
            type: VideosCell,
            data: vid,
        }))
    }
}
