import { Lightning } from '@lightningjs/sdk'
import { clamp } from '../../../utils'
import { Key } from '../Keys'

export default class KeyGroup extends Lightning.Component {
    protected _index = 0
    keyWidth = 90
    keyHeight = 90
    keySpacing = 0
    keyBackgroundColor = 0x00ffffff

    _getFocused(): Lightning.Element {
        return this.activeKey
    }

    protected _setIndex(index: number): boolean {
        if (index < 0 || index > this.keys.length - 1) {
            return false
        } else {
            this._index = clamp(index, 0, this.keys.length - 1)
            this.signal('onIndexChanged', this._index)
            return true
        }
    }

    get keys(): Lightning.Element[] {
        return this.children
    }

    get activeKey(): Key {
        return this.keys[this.index] as Key
    }

    get index(): number {
        return this._index
    }

    set index(value: number) {
        this._setIndex(value)
    }

    _handleLeft(): boolean {
        return this._setIndex(this.index - 1)
    }

    _handleRight(): boolean {
        return this._setIndex(this.index + 1)
    }
}
