import { Lightning, Template, Utils } from '@lightningjs/sdk'

export default class Screensaver extends Lightning.Component {
    static _template(): Template {
        return {
            w: 1920,
            h: 1080,
            alpha: 0,
            Background: {
                w: (w: number) => w,
                h: (h: number) => h,
                src: Utils.asset('images/splash_fhd.jpg'),
            },
            Label: {
                w: (w: number) => w,
                h: (h: number) => h,
                y: 750,
                text: {
                    text: 'Screensaver',
                    textAlign: 'center',
                    fontSize: 200,
                    highlight: false,
                },
            },
        }
    }

    _focus(): void {
        this.setSmooth('alpha', 1)
    }

    _unfocus(): void {
        this.setSmooth('alpha', 0)
    }
}
