import { Lightning, Animation, Template } from '@lightningjs/sdk'
import { Video } from '../Data/Models'

export default class Bumper extends Lightning.Component {
    _transition?: Animation

    private static readonly width = 1920
    private static readonly height = 1080

    static _template(): Template {
        return {
            BottomLayer: {
                x: Bumper.width,
                w: Bumper.width,
                h: Bumper.height,
                rect: true,
                color: 0xffffffff,
            },
            MiddleLayer: {
                x: Bumper.width,
                w: Bumper.width,
                h: Bumper.height,
                rect: true,
                color: 0xff3061ff,
            },
            TopLayer: {
                x: Bumper.width,
                w: Bumper.width,
                h: Bumper.height,
                rect: true,
                color: 0xff102039,
            },
            VideoTitle: {
                x: Bumper.width,
                w: Bumper.width,
                h: Bumper.height,
                UpNext: {
                    x: 60,
                    y: 331,
                    text: {
                        text: 'UP NEXT',
                        fontFace: 'FoundersGroteskCondensed-Regular',
                        fontSize: 53,
                        cutSy: 19,
                    },
                    Underline: {
                        y: 60,
                        w: 72,
                        h: 16,
                        rect: true,
                    },
                },
                Title: {
                    x: 92,
                    y: 466,
                    text: {
                        text: '',
                        fontFace: 'FoundersGroteskCondensed-Bold',
                        wordWrapWidth: 400,
                        fontSize: 50,
                        cutSy: 18,
                    },
                },
            },
        }
    }

    //
    // components event's
    _init(): void {
        const xFirstStop = Bumper.width - 568
        const xSecondStop = -1800

        this._transition = this.animation({
            duration: 6,
            stopMethod: 'immediate',
            actions: [
                {
                    t: 'TopLayer',
                    p: 'x',
                    v: {
                        sm: 0,
                        0: Bumper.width,
                        0.1: xFirstStop,
                        0.4: xFirstStop,
                        0.6: 0,
                        0.7: 0,
                        0.9: xSecondStop,
                        1: -Bumper.width,
                    },
                },
                {
                    t: 'MiddleLayer',
                    p: 'x',
                    v: {
                        sm: 0,
                        0: Bumper.width,
                        0.6: Bumper.width,
                        0.7: 0,
                        0.925: xSecondStop,
                        1: -Bumper.width,
                    },
                },
                {
                    t: 'BottomLayer',
                    p: 'x',
                    v: {
                        sm: 0,
                        0: Bumper.width,
                        0.6: Bumper.width,
                        0.7: 0,
                        0.95: xSecondStop,
                        1: -Bumper.width,
                    },
                },
                {
                    t: 'VideoTitle',
                    p: 'x',
                    v: {
                        sm: 0,
                        0: Bumper.width,
                        0.1: xFirstStop,
                        0.7: xFirstStop,
                        0.9: -520,
                        1: -640,
                    },
                },
                {
                    t: 'VideoTitle',
                    p: 'alpha',
                    v: { sm: 0, 0: 1, 0.8: 1, 0.9: 0 },
                },
            ],
        })
    }

    set item(video: Video) {
        if (this._transition && !this._transition.isPlaying()) {
            const titleTag = this.tag('VideoTitle').tag('Title').text
            if (titleTag) {
                titleTag.text = video.headline
            }
            this._transition.start()
        }
    }
}
