import { Lightning, States, Template } from '@lightningjs/sdk'
import Events from '../Events'
import NowPlayingTray from './NowPlayingTray'
import PlayerControls from './PlayerControls'
import Screensaver from '../components/ScreenSaver'
import Timer from '../components/Timer'

export default class PlayerUI extends Lightning.Component {
    private readonly minutesToShowScreenSaver = 15
    private _screenSaverTimer: Timer = new Timer(this, this.minutesToShowScreenSaver * 60, () =>
        this._setState('Screensaver')
    )

    static _template(): Template {
        return {
            PlayerControls: {
                type: PlayerControls,
                signals: {
                    playerState: true,
                    screenKey: true,
                },
            },
            NowPlayingTray: {
                type: NowPlayingTray,
                signals: {
                    autohide: '$nowPlayingTrayAutohide',
                    screenKey: true,
                },
            },
            Screensaver: {
                type: Screensaver,
            },
        }
    }

    static _states(): States {
        return [
            class PlayerControlsFocused extends this {
                _getFocused() {
                    return this.tag('PlayerControls')
                }
                _handleUp(): void {
                    this._setState('NowPlayingTrayFocused')
                }
                _handleDown(): void {
                    this._setState('NowPlayingTrayFocused')
                }
            },
            class NowPlayingTrayFocused extends this {
                _getFocused() {
                    return this.tag('NowPlayingTray')
                }
                _handleUp(): void {
                    this._setState('PlayerControlsFocused')
                }
                _handleDown(): void {
                    this._setState('PlayerControlsFocused')
                }
                _handleBack(): void {
                    this._setState('PlayerControlsFocused')
                }
            },
            class Screensaver extends this {
                _getFocused() {
                    return this.tag('Screensaver')
                }
                _captureKey(): void {
                    this._setState('PlayerControlsFocused')
                    this._resetTimer()
                }
            },
        ]
    }

    _init(): void {
        this._setState('PlayerControlsFocused')
        Events.on('$app.cellSelected', this.$cellSelected.bind(this))
    }

    private _resetTimer() {
        if (this.screensaver.visible) {
            this._screenSaverTimer.start()
        } else {
            this._screenSaverTimer.stop()
        }
    }

    private async $cellSelected(): Promise<void> {
        this._setState('PlayerControlsFocused')
    }

    private $nowPlayingTrayAutohide(): void {
        this._setState('PlayerControlsFocused')
    }

    private screenKey(): void {
        this._resetTimer()
    }

    private playerState(state: string): void {
        this.screensaver.visible = state === 'Paused' || state === 'Seeking'
        this._resetTimer()
    }

    private get screensaver() {
        return this.tag('Screensaver') as Screensaver
    }
}
