import { Lightning, Template } from '@lightningjs/sdk'
import { Column, Row } from 'lightning-ui-components'
import { PlaylistCell } from '../../components/cells'
import CreateRow from '../../components/rows'
import PlaylistRow from '../../components/rows/PlaylistRow'
import { Playlist, Video } from '../../Data/Models'
import Events from '../../Events'
import { clamp } from '../../utils'
import { CellSelectedPayload } from '../../ViewModels/ViewModels'

export default class SearchPageContent extends Lightning.Component {
    private static readonly gridSpacing = 69
    private _channel?: Playlist

    static _template(): Template {
        const offset = 22

        return {
            alpha: 0,
            clipping: true,
            Column: {
                w: (w: number) => w,
                h: (h: number) => h,
                x: offset,
                y: offset,
                type: Column,
                itemSpacing: SearchPageContent.gridSpacing,
            },
            HiddenRow: {},
        }
    }

    _getFocused(): Lightning.Element {
        return this.selectedCell
    }

    private _setRowIndex(columnIndex: number, rowIndex: number): void {
        columnIndex = clamp(columnIndex, 0, this.column.items.length - 1)
        const row = this.column.items[columnIndex] as Row
        row.selectedIndex = clamp(rowIndex, 0, row.items.length - 1)
    }

    private get column() {
        return this.tag('Column') as Column
    }

    private get selectedRow() {
        return this.column.items[this.column.selectedIndex] as Row
    }

    private get selectedCell() {
        return this.selectedRow.selected as PlaylistCell
    }

    get channel(): Playlist | undefined {
        return this._channel
    }

    set channel(channel: Playlist | undefined) {
        this._channel = channel

        if (!channel) {
            this.setSmooth('alpha', 0)
            return
        }

        const cells = channel.videos.map((video: Video) => {
            return {
                type: PlaylistCell,
                data: video,
            }
        })

        const rows = []

        for (let i = 0; i < cells.length; i += 2) {
            rows.push({
                type: Row,
                itemSpacing: SearchPageContent.gridSpacing,
                items: i + 1 < cells.length ? [cells[i], cells[i + 1]] : [cells[i]],
            })
        }

        this.column.items = rows
        this.setSmooth('alpha', 1)

        this.patch({ HiddenRow: undefined })
        this.patch({ HiddenRow: { ...CreateRow(channel), alpha: 0 } })
    }

    private _captureUp(): boolean {
        this._setRowIndex(this.column.selectedIndex - 1, this.selectedRow.selectedIndex)
        return this.column.selectPrevious()
    }

    private _captureDown(): boolean {
        this._setRowIndex(this.column.selectedIndex + 1, this.selectedRow.selectedIndex)
        return this.column.selectNext()
    }

    private _handleEnter(): void {
        const hiddenRow = this.tag('HiddenRow') as PlaylistRow
        hiddenRow.selectedIndex = this.column.selectedIndex * 2 + this.selectedRow.selectedIndex

        Events.emit('$app.cellSelected', new CellSelectedPayload(hiddenRow, this.selectedCell))
    }
}
