import { TypedEmitter } from 'tiny-typed-emitter'
import { Video } from './Data/Models'
import { CellSelectedPayload } from './ViewModels/ViewModels'

export interface PlayerEvent {
    type: string
    timestamp: number
}

interface AppEvents {
    '$app.cellSelected': (payload: CellSelectedPayload) => void
    '$app.videoSelected': (payload: Video, resumeTime?: number) => void
    '$app.firstRoute': (payload: string) => void
    '$player.playerEvent': (payload: PlayerEvent) => void
}

export default new TypedEmitter<AppEvents>()
