import { Lightning, Template } from '@lightningjs/sdk'
import { TabMenuItemError } from '../../Errors'

export default class TabMenuItem extends Lightning.Component {
    static focusColor = 0xff000000
    static unfocusColor = 0xffc1c1c1
    static activeColor = 0xffffffff

    private _useActiveColor = false

    static _template(): Template {
        return {
            Background: {
                visible: false,
            },
            Image: {
                mount: 0.5,
                color: TabMenuItem.unfocusColor,
            },
        }
    }

    //
    // callbacks
    _focus(): void {
        this.patch({
            Image: {
                color: TabMenuItem.focusColor,
            },
            Background: {
                visible: true,
            },
        })
    }

    _unfocus(): void {
        this._updateImageColor()

        this.patch({
            Background: {
                visible: false,
            },
        })
    }

    //
    // override
    onSelected: () => never = (): never => {
        throw new TabMenuItemError()
    }

    //
    // helpers
    private _updateImageColor() {
        if (this.hasFocus()) {
            return
        }

        this.patch({
            Image: {
                color: this.useActiveColor ? TabMenuItem.activeColor : TabMenuItem.unfocusColor,
            },
        })
    }

    //
    // properties
    set image(value: string) {
        this.patch({
            Image: {
                src: value,
                x: this.w / 2,
                y: this.h / 2,
            },
        })
    }

    get useActiveColor(): boolean {
        return this._useActiveColor
    }

    set useActiveColor(value: boolean) {
        this._useActiveColor = value
        this._updateImageColor()
    }
}
