import { Lightning, Log, Router, Template, VideoPlayer } from '@lightningjs/sdk'
import { BrandName, CardPageData, Playlist, Position } from '../../Data/Models'
import CreateRow from '../../components/rows'
import { Column } from 'lightning-ui-components'
import { CellSelectedPayload } from '../../ViewModels/ViewModels'
import SplitPage from '../SplitPage'
import CardPageTop from './CardPageTop'
import Events from '../../Events'
import Store from '../../Store'
import { clamp } from '../../utils'

export default class CardPage extends SplitPage<CardPageData> {
    static _template(): Template {
        return {
            Background: {
                w: 1920,
                h: 1080,
                rect: true,
                color: 0xffffffff,
            },
            Fade: {
                TopLeftCorner: {
                    w: 1920 / 2,
                    h: 1080 / 2,
                    rect: true,
                    color: 0x00000000,
                    colorUl: 0x7c000000,
                },
                Bottom: {
                    w: 1920,
                    h: 1080,
                    rect: true,
                    color: 0x00000000,
                    colorBottom: 0xed000000,
                },
            },
            ...super._template(),
            landingComponent: {
                type: CardPageTop,
            },
            contentComponent: {
                y: 20,
                itemSpacing: 69,
                type: Column,
            },
        }
    }

    //
    // callbacks
    _init(): void {
        Events.on('$app.cellSelected', this.$cellSelected.bind(this))
    }

    _onDataProvided(): void {
        this._render()
    }

    _getFocused(): Lightning.Element {
        return this
    }

    //
    // helpers
    private _render(): void {
        const rows = this.data.rows.map((row: Playlist) => CreateRow(row))

        this.cardPageTop.patch({
            title: this.data.title,
            subtitle: this.data.subtitle,
            item: rows[0],
        })

        this._contentSettings.enabled = rows.length > 1

        this.column.patch({
            items: rows.length > 1 ? rows.slice(1) : undefined,
        })

        const storeHasPosition = Store.isReady && this.data.cardId == Store.selectedGrid.id
        const position = storeHasPosition ? Store.selectedGrid.position : { x: 0, y: 0 }

        this._focusCell(position)

        if (this.data.backgroundUrl) {
            this.tag('Background').src = this.data.backgroundUrl
        }
    }

    private _focusCell({ x, y }: Position): void {
        if (y === 0) {
            this.cardPageTop.item.selectedIndex = x
            this._setState('LandingFocused')
        } else {
            y = clamp(y - 1, 0, this.column.items.length - 1)

            this.column.selectedIndex = y
            this.column.selected.row.selectedIndex = x
            this._setState('ContentFocused')
        }
    }

    //
    // events
    async $cellSelected(payload: CellSelectedPayload): Promise<void> {
        if (this.active) {
            Log.info('CardPage $cellSelected >', payload)
            const videoIndex = payload.row.selectedIndex
            Store.selectedGrid = {
                id: this.data.cardId,
                channels: this.data.rows,
                position: {
                    x: videoIndex,
                    y: this.channelIndex,
                },
            }

            this._backToHome({
                navigateBack: true,
                hiddenOnLoad: true,
            })
        }
    }

    _handleBack(): void {
        this._backToHome({
            contentFocusedOnLoad: true,
        })

        VideoPlayer.play()
    }

    private _backToHome(params: {
        navigateBack?: boolean
        hiddenOnLoad?: boolean
        contentFocusedOnLoad?: boolean
    }): void {
        const path = 'brand/' + (this.data.activeBrand || BrandName.nbcnews)
        Router.navigate(path, {
            ...params,
            skipAutoPlay: true,
            focusOnLastItem: true,
        })
    }

    //
    // properties
    private get column() {
        return this.contentComponent as Column
    }

    private get channelIndex() {
        return this._getState() == 'LandingFocused' ? 0 : this.column.selectedIndex + 1
    }

    private get cardPageTop() {
        return this.landingComponent as CardPageTop
    }
}
