import { Lightning, Template, Utils } from '@lightningjs/sdk'
import LoadingIndicator from '../components/LoadingIndicator'

export default class Buffering extends Lightning.Component {
    static _template(): Template {
        return {
            Background: {
                src: Utils.asset('images/background.png'),
            },
            LoadingIndicator: {
                x: 1920 / 2,
                y: 1080 / 2,
                type: LoadingIndicator,
            },
            Arrow: {
                x: 1920 / 2,
                y: 1080,
                mountX: 0.5,
                src: Utils.asset('images/player/down_arrow.png'),
            },
        }
    }
    _enable(): void {
        this.tag('Arrow').setSmooth('y', 1000, { delay: 1, duration: 1 })
    }
}
