import mParticle from '@mparticle/web-sdk'
import { Settings } from '@lightningjs/sdk'
import Platform from '../Platform'

class Analytics {
    public init() {
        this.initMParticle()
        this.initErrorHandler()
    }

    private initMParticle() {
        const mParticleConfig = {
            isDevelopmentMode: Settings.get('app', 'debug'),
            //logLevel: 'verbose',
            appName: 'NBC News X1',
            appVersion: Settings.get('app', 'version'),
        }
        mParticle.init('us1-0d3f7d9056927d47bb2cac6a11dee1c5', mParticleConfig)
    }

    private initErrorHandler() {
        window.onerror = function(message, source, lineno, colno, error) {
            const errorMessage =
                typeof message === 'string' ? message : (message as ErrorEvent).message
            sharedInstance.error('Unhandled Exception', {
                message: errorMessage,
                location: `${source}: (${lineno}, ${colno})`,
                error: JSON.stringify(error),
            })
        }

        window.addEventListener('error', function(event: ErrorEvent) {
            sharedInstance.error('Unhandled Exception', {
                message: event.message,
                location: `${event.filename}: (${event.lineno}, ${event.colno})`,
                error: event.error ? JSON.stringify(event.error.stack) : '',
            })
        })
    }

    public error(error: Error | string, optional = {}): void {
        const errorType = error instanceof Error ? error.name : error
        const errorMetadata = {
            ...(error instanceof Error ? formatErrorMetadata(error) : {}),
            ...optional,
        }
        mParticle.logEvent('Error', mParticle.EventType.Other, {
            type: errorType,
            ...errorMetadata,
        })
        const errorMetricsParams = {
            visible: true,
            code: 'unhandled_exception',
            ...errorMetadata,
        }
        Platform.errorMetricsHandler(errorType, errorMetricsParams.visible, errorMetricsParams.code)
    }

    public appLaunch(): void {
        mParticle.logEvent('App Launch', mParticle.EventType.Navigation)
    }

    public contentLoaded(id: string, url: string, etag = ''): void {
        mParticle.logEvent('Content Loaded', mParticle.EventType.Other, {
            id: id,
            url: url,
            etag: etag,
        })
    }
}

function formatErrorMetadata(error: Error) {
    let location = ''
    if (error.stack) {
        try {
            const matched = error.stack.match(/\/([\/\w-_\.]+\.js):(\d*):(\d*)/)
            if (matched) {
                const [, filename, line, column] = matched
                location = `${filename}: (${line}, ${column})`
            }
        } catch (error) {
            // Could not get file info from error
        }
    }
    return {
        message: error.message,
        location: location,
        error: error.stack,
    }
}

const sharedInstance = new Analytics()

export default sharedInstance
