import { Template } from '@lightningjs/sdk'
import BaseCell from './BaseCell'
import { NBCDAimsUrl } from '../../utils'

export default class PagesCell extends BaseCell {
    private static width = 386
    private static height = 140

    static _template(): Template {
        return {
            ...PagesCell._getBaseTemplate(PagesCell.width, PagesCell.height),
        }
    }

    protected _render(): void {
        if ('image' in this.data) {
            this.image.src = NBCDAimsUrl(this.data.image, PagesCell.width, PagesCell.height)
        }
    }
}
