import { Animation, Lightning, Template } from '@lightningjs/sdk'

export default class NowPlayingIndicator extends Lightning.Component {
    private _animB1?: Animation
    private _animB2?: Animation
    private _animB3?: Animation
    private _animB4?: Animation

    public static width = 55
    public static height = 39

    static _template(): Template {
        const bar = {
            y: 30,
            mountY: 1,
            w: 6,
            h: 22,
            rect: true,
        }

        return {
            Background: {
                w: 55,
                h: 39,
                color: 0xb0000000,
                rect: true,
                shader: {
                    type: Lightning.shaders.RoundedRectangle,
                    radius: 10,
                },
            },
            Bar1: {
                ...bar,
                x: 10,
            },
            Bar2: {
                ...bar,
                x: 20,
            },
            Bar3: {
                ...bar,
                x: 30,
            },
            Bar4: {
                ...bar,
                x: 40,
            },
        }
    }

    _init(): void {
        const action = {
            p: 'h',
            v: { 0: 22, 0.5: 0, 1: 22 },
        }
        this._animB1 = this.tag('Bar1').animation({
            duration: 1,
            repeat: -1,
            actions: [action],
        })
        this._animB2 = this.tag('Bar2').animation({
            duration: 0.6,
            delay: 0.1,
            repeat: -1,
            actions: [action],
        })
        this._animB3 = this.tag('Bar3').animation({
            duration: 1.2,
            delay: 0.3,
            repeat: -1,
            actions: [action],
        })
        this._animB4 = this.tag('Bar4').animation({
            duration: 0.6,
            delay: 0.6,
            repeat: -1,
            actions: [action],
        })
    }

    _active(): void {
        this._animB1 && this._animB1.start()
        this._animB2 && this._animB2.start()
        this._animB3 && this._animB3.start()
        this._animB4 && this._animB4.start()
    }

    _inactive(): void {
        this._animB1 && this._animB1.finish()
        this._animB2 && this._animB2.finish()
        this._animB3 && this._animB3.finish()
        this._animB4 && this._animB4.finish()
    }
}
