import { Lightning, Template } from '@lightningjs/sdk'

export default class HomePageTop extends Lightning.Component {
    static _template(): Template {
        const w = 1920
        const h = 1080

        return {
            w: w,
            h: h,
            Background: {
                x: 0,
                y: 0,
                w: w,
                h: h,
                rect: true,
                colorTop: 0x00000000,
                colorBottom: 0xff000000,
            },
            Item: {},
        }
    }

    _init(): void {
        this._setState('ItemFocused')
    }

    _getFocused(): Lightning.Element {
        return this.item
    }

    get item(): Lightning.Element {
        return this.tag('Item')
    }

    set item(value: Lightning.Element) {
        this.patch({ Item: undefined })
        this.patch({ Item: value })

        this.item.y = 930 - this.item.h
    }
}
