import BaseRow from './BaseRow'
import PlaylistCell from '../cells/PlaylistCell'
import { Playlist, Video } from '../../Data/Models'

export default class PlaylistRow extends BaseRow {
    protected _getCells(): { type: typeof PlaylistCell; data: Video }[] {
        return (this.data as Playlist).videos.map((vid: Video) => ({
            type: PlaylistCell,
            data: vid,
        }))
    }
}
