import { Lightning, Template, Utils, Router } from '@lightningjs/sdk'
import Analytics from '../Analytics'

export default class ErrorPage extends Lightning.Component {
    static _template(): Template {
        return {
            Background: {
                w: 1920,
                h: 1080,
                src: Utils.asset('images/background.png'),
            },
            Label: {
                w: 1920,
                y: 1080 / 2,
                mountY: 0.5,
                text: {
                    text: 'Error',
                    textAlign: 'center',
                    fontSize: 200,
                    highlight: false,
                },
            },
        }
    }

    set params({ request }: { request?: Router.Request }) {
        const error = request && request.error
        if (error) {
            Analytics.error(error)
        } else {
            Analytics.error('Unhandled Exception')
        }
    }
}
