import { Playlist, Channel, Video, CellGrid } from './Data/Models'
import { StoreDataError } from './Errors'

export type QsParams = { [index: string]: string }

type AppFlags = {
    playingDeeplinkedVideo: boolean
}

class Store {
    private _contentGrid?: CellGrid
    private _selectedGrid?: CellGrid
    flags: AppFlags = {
        playingDeeplinkedVideo: false,
    }
    qs: QsParams = {}

    get contentGrid(): CellGrid {
        if (this._contentGrid) return this._contentGrid
        throw new StoreDataError()
    }

    set contentGrid(value: CellGrid) {
        this._contentGrid = value
    }

    get selectedGrid(): CellGrid {
        return this._selectedGrid || this.contentGrid
    }

    set selectedGrid(value: CellGrid) {
        this._selectedGrid = value
    }

    get selectedChannel(): Channel {
        return this._getCurrentChannelInGrid(this.selectedGrid)
    }

    get selectedVideo(): Video | null {
        return this._getCurrentVideoInGrid(this.selectedGrid)
    }

    get nextSelectedGrid(): CellGrid {
        return this._getNextGridState(this.selectedGrid)
    }

    get nextVideo(): Video | null {
        if (this.flags.playingDeeplinkedVideo) return this.firstContentVideo
        return this._getCurrentVideoInGrid(this.nextSelectedGrid)
    }

    get firstContentVideo(): Video | null {
        const firstChannel = this.contentPlaylistChannels[0]
        return firstChannel ? firstChannel.videos[0] : null
    }

    get contentPlaylistChannels(): Playlist[] {
        return this.contentGrid.channels
            .filter(channel => 'videos' in channel)
            .map(channel => channel as Playlist)
    }

    get isReady(): boolean {
        return this._contentGrid != undefined
    }

    selectNextVideoOrChannel(): void {
        this.selectedGrid = this.nextSelectedGrid
    }

    private _getCurrentChannelInGrid(grid: CellGrid): Channel {
        return grid.channels[grid.position.y]
    }

    private _getCurrentVideoInGrid(grid: CellGrid): Video | null {
        const channel = this._getCurrentChannelInGrid(grid)
        const { x: index } = grid.position
        if ('videos' in channel) {
            return channel.videos[index] || null
        }
        return null
    }

    private _getNextGridState(grid: CellGrid): CellGrid {
        const { channels } = grid
        const { x, y } = grid.position
        for (let i = y; i < channels.length; i++) {
            const channel = channels[i]
            if ('videos' in channel) {
                const isCurrentChannel = i === y
                const nextVideoIndex = isCurrentChannel ? x + 1 : 0
                if (nextVideoIndex < channel.videos.length) {
                    return {
                        channels: channels,
                        position: {
                            x: nextVideoIndex,
                            y: i,
                        },
                    }
                }
            }
        }
        return {
            channels: this.contentGrid.channels,
            position: {
                x: 0,
                y: 0,
            },
        }
    }
}

export default new Store()
