import { Lightning, Utils, Template } from '@lightningjs/sdk'
import { Video } from '../Data/Models'
import { NBCDAimsUrl } from '../utils'

const BAR = {
    w: 1920 / 2,
    h: 15,
    x: 1920 / 4,
    y: 750,
    backgroundColor: 0xff636363,
    color: 0xffff503c, // TODO: Is this color dynamic? The figma suggests each brand might have a different color, but that's not present on the roku app
}

export default class Loading extends Lightning.Component {
    _progress = 0
    static _template(): Template {
        return {
            alpha: 0,
            forceZIndexContext: true,
            zIndex: -10,
            Background: {
                w: 1920,
                h: 1080,
                rect: true,
                color: 0xffffffff,
            },
            Image: {
                w: 1920,
                h: 1080,
            },
            Gradient: {
                w: 1920,
                h: 1080,
                rect: true,
                colorTop: 0x00000000,
                colorBottom: 0xff000000,
            },
            ProgressBar: {
                x: BAR.x,
                y: BAR.y,
                Background: {
                    texture: Lightning.Tools.getRoundRect(
                        BAR.w,
                        BAR.h,
                        5,
                        0,
                        0,
                        true,
                        BAR.backgroundColor
                    ),
                },
                Progress: {
                    w: 0,
                    texture: Lightning.Tools.getRoundRect(BAR.w, BAR.h, 5, 0, 0, true, BAR.color),
                },
            },
            TimeStamps: {
                x: 1920 / 4,
                y: 790,
                Date: {
                    text: {
                        text: '',
                        fontSize: 20,
                    },
                },
                Duration: {
                    x: 1920 / 2,
                    mountX: 1,
                    text: {
                        text: '',
                        fontSize: 20,
                        textAlign: 'right',
                    },
                },
            },
            Title: {
                x: 1920 / 4,
                y: 830,
                text: {
                    text: '',
                    wordWrapWidth: 1920 / 2,
                    fontSize: 35,
                },
            },
            Arrow: {
                x: 1920 / 2,
                y: 1080,
                mountX: 0.5,
                src: Utils.asset('images/player/down_arrow.png'),
            },
        }
    }

    set item(video: Video) {
        this.patch({
            Image: { src: NBCDAimsUrl(video.tease, 1920, 1080) },
            TimeStamps: {
                // TODO: Reformat these strings to put them up to spec with figma
                Date: { text: { text: video.published.toString().substring(0, 10) } },
                Duration: { text: { text: video.duration } },
            },
            Title: { text: { text: video.headline } },
            Arrow: { y: 1080 },
            ProgressBar: {
                Progress: {
                    w: Math.floor(BAR.w * 0.1),
                },
            },
        })

        this.tag('Arrow').setSmooth('y', 1000, { delay: 1, duration: 1 })
    }

    set progress(progress: number) {
        if (progress > this._progress) {
            this._progress = progress
            this.tag('ProgressBar')
                .tag('Progress')
                .patch({
                    w: Math.floor(BAR.w * this._progress * 0.01),
                })
        }
    }
}
