import { Lightning, Template, Router, Utils, States, Log } from '@lightningjs/sdk'
import Keyboard from '../../components/keyboard/Keyboard'
import { BrandName, Playlist, Video } from '../../Data/Models'
import Events from '../../Events'
import Store from '../../Store'
import { CellSelectedPayload, ChannelRow } from '../../ViewModels/ViewModels'
import BasePage from '../BasePage'
import SearchPageContent from './SearchPageContent'

export default class SearchPage extends BasePage<ChannelRow> {
    fetchSearchQuery?: (query: string) => Promise<Video[]>

    private params = {
        query: '',
    }

    static _template(): Template {
        const width = 1920
        const height = 1080
        const keyboardColumnWidth = 853
        const divY = 237
        const underlineY = 65
        const underlineHeight = 9
        const underlineContentGap = 29
        const contentX = keyboardColumnWidth + 105
        const contentY = underlineY + underlineHeight + underlineContentGap

        return {
            w: width,
            h: height,
            Background: {
                w: (w: number) => w,
                h: (h: number) => h,
                src: Utils.asset('images/search-page-background.png'),
            },
            Div: {
                w: (w: number) => w,
                h: (h: number) => h - divY,
                y: divY,
                Keyboard: {
                    x: keyboardColumnWidth / 2,
                    mountX: 0.5,
                    type: Keyboard,
                    signals: {
                        onDone: true,
                        onValueChanged: true,
                    },
                },
                Header: {
                    x: keyboardColumnWidth + 126,
                    text: {
                        text: 'SEARCH',
                        cutSy: 21,
                        fontSize: 60,
                        fontFace: 'FoundersGroteskCondensed-Semibold',
                    },
                    Underline: {
                        w: 111,
                        h: underlineHeight,
                        y: underlineY,
                        rect: true,
                        color: 0xffffffff,
                    },
                },
                Content: {
                    w: (w: number) => w - contentX,
                    h: (h: number) => h - contentY,
                    x: contentX,
                    y: contentY,
                    type: SearchPageContent,
                },
                Logo: {
                    x: contentX + (width - contentX) / 2,
                    y: (height - divY) / 2,
                    mount: 0.5,
                    alpha: 0,
                    src: Utils.asset('images/search-page-logo-placeholder.png'),
                },
            },
        }
    }

    static _states(): States {
        return [
            class KeyboardFocused extends this {
                _getFocused(): Lightning.Element {
                    return this.keyboard
                }
                _handleBack(): void {
                    Router.focusWidget('TabMenu')
                }
                _handleUp(): void {
                    Router.focusWidget('TabMenu')
                }
                _handleRight(): boolean {
                    if (this.content.channel) {
                        this._setState('ContentFocused')
                        return true
                    }
                    return false
                }
            },
            class ContentFocused extends this {
                _getFocused(): Lightning.Element {
                    return this.content
                }
                _handleUp(): void {
                    Router.focusWidget('TabMenu')
                }
                _handleLeft(): void {
                    this._setState('KeyboardFocused')
                }
            },
        ]
    }

    _init(): void {
        this._setState('KeyboardFocused')
        Events.on('$app.cellSelected', this.$cellSelected.bind(this))
    }

    _firstEnable(): void {
        const urlDeeplinkQuery = Store.qs.query
        if (urlDeeplinkQuery) {
            this.keyboard.value = urlDeeplinkQuery
        }
    }

    _onDataProvided(): void {
        const launchpointDeeplinkQuery = this.params.query
        if (launchpointDeeplinkQuery) {
            this.keyboard.value = launchpointDeeplinkQuery
        }
    }

    private _backToHome(): void {
        const path = 'brand/' + BrandName.nbcnews
        Router.navigate(path, {
            hiddenOnLoad: true,
            skipAutoPlay: true,
            focusOnLastItem: true,
            navigateBack: true,
        })
    }

    async $cellSelected(payload: CellSelectedPayload): Promise<void> {
        if (this.active && this.content.channel) {
            Log.info('SearchPage $cellSelected >', payload)

            const videoIndex = payload.row.selectedIndex
            Store.selectedGrid = {
                channels: [this.content.channel],
                position: {
                    x: videoIndex,
                    y: 0,
                },
            }

            this._backToHome()
        }
    }

    private async onValueChanged(value: string): Promise<void> {
        if (value.length == 0) {
            this.content.channel = undefined
            this.headerText = 'SEARCH'
            return
        }

        if (value.length < 3 || !this.fetchSearchQuery) {
            return
        }

        this.content.channel = undefined
        this.headerText = 'SEARCHING...'
        this.logo.setSmooth('alpha', 1)

        const videos = await this.fetchSearchQuery(value)
        this.logo.setSmooth('alpha', 0)

        if (videos.length > 0) {
            this.content.channel = {
                type: 'playlist',
                title: `RESULTS FOR "${value}"`,
                videos: videos,
            } as Playlist

            this.headerText = `${videos.length} ITEMS FOUND`
        } else {
            this.headerText = 'NO ITEMS FOUND'
        }
    }

    private get keyboard() {
        return this.tag('Div.Keyboard') as Keyboard
    }

    private get content() {
        return this.tag('Div.Content') as SearchPageContent
    }

    private get logo() {
        return this.tag('Div.Logo')
    }

    private set headerText(value: string) {
        this.tag('Div.Header').patch({ text: value })
    }
}
