import { LayoutKeyData } from '../../../Data/Models'
import { Key } from '../Keys'
import KeyGroup from './KeyGroup'

export default class CharacterKeyGroup extends KeyGroup {
    private _layout?: LayoutKeyData
    columnCount = 10

    get activeKey(): Key {
        return super.activeKey
    }

    set layout(value: LayoutKeyData) {
        if (this._layout == value) {
            return
        }

        this._layout = value

        const chars = value.characters.map((label: string, i: number) => {
            return {
                type: Key,
                label: label,
                x: (i % this.columnCount) * this.keyWidth,
                y: Math.floor(i / this.columnCount) * this.keyHeight,
                w: this.keyWidth - this.keySpacing * 2,
                h: this.keyHeight - this.keySpacing * 2,
                backgroundColor: this.keyBackgroundColor,
            }
        })

        this.patch({ children: undefined })
        this.patch({ children: chars })

        this.index = 0
        this.signal('onReady')
    }

    _handleRight(): boolean {
        const realIndex = this._index
        const rowIndex = realIndex % this.columnCount

        if (rowIndex < this.columnCount - 1 && realIndex < this.keys.length - 1) {
            this.index = realIndex + 1
            return true
        } else {
            return false
        }
    }

    _handleLeft(): boolean {
        const realIndex = this._index
        const rowIndex = realIndex % this.columnCount

        if (rowIndex > 0) {
            this.index = realIndex - 1
            return true
        } else {
            return false
        }
    }

    _handleUp(): boolean {
        const realIndex = this._index
        const newIndex = realIndex - this.columnCount

        if (newIndex >= 0) {
            this.index = newIndex
            return true
        } else {
            return false
        }
    }

    _handleDown(): boolean {
        const realIndex = this._index
        const newIndex = realIndex + this.columnCount

        if (newIndex < this.keys.length) {
            this.index = newIndex
            return true
        } else {
            return false
        }
    }

    _handleEnter(): void {
        this.signal('onCharacterKeyPressed', this.activeKey.label)
    }
}
