import BaseRow from './BaseRow'
import PagesCell from '../cells/PagesCell'
import { Cards, Card } from '../../Data/Models'

export default class PagesRow extends BaseRow {
    protected _getCells(): { type: typeof PagesCell; data: Card }[] {
        return (this.data as Cards).cards.map((card: Card) => ({
            type: PagesCell,
            data: card,
        }))
    }
}
