import { Lightning, Template } from '@lightningjs/sdk'
import CreateRow from '../components/rows'
import BaseRow from '../components/rows/BaseRow'
import Timer from '../components/Timer'
import { Channel } from '../Data/Models'
import Events from '../Events'
import Store from '../Store'
import { CellSelectedPayload } from '../ViewModels/ViewModels'

export default class NowPlayingTray extends Lightning.Component {
    private _selectedChannel?: Channel
    private _autohideTimer = new Timer(this, 20, () => {
        if (this.hasFocus()) {
            this.signal('autohide')
        }
    })

    static _template(): Template {
        const w = 1920
        const h = 1080

        return {
            x: 0,
            y: h,
            w,
            h,
            Gradient: {
                w: (w: number) => w,
                h: (h: number) => h,
                rect: true,
                colorTop: 0x00000000,
                colorBottom: 0xff000000,
            },
            Item: {},
        }
    }

    _init(): void {
        Events.on('$app.cellSelected', this.$cellSelected.bind(this))
    }

    _firstEnable(): void {
        this._updateCurrentItem()
    }

    _getFocused(): Lightning.Element {
        return this.item
    }

    _focus(): void {
        this.yPosition = 0
        this._autohideTimer.start()
        this._updateCurrentItem()
    }

    _unfocus(): void {
        this.yPosition = this.h
        this._autohideTimer.stop()
    }

    _captureKey(): boolean {
        this.signal('screenKey')
        this._autohideTimer.start()
        return false
    }

    private async $cellSelected(payload: CellSelectedPayload): Promise<void> {
        if (this.hasFocus()) {
            const selectedVideoIndex = payload.row.selectedIndex
            Store.selectedGrid.position.x = selectedVideoIndex
        }
    }

    private _updateCurrentItem() {
        const selectedChannel = Store.selectedChannel

        if (this._selectedChannel != selectedChannel) {
            this._selectedChannel = selectedChannel

            this.patch({ Item: undefined })
            this.patch({ Item: CreateRow(this._selectedChannel) })
            this.item.titleText = 'NOW PLAYING: ' + this.item.titleText
        }

        const padding = 20
        const h = this.item.h + padding * 2
        const y = this.h - h
        const selectedVideoIndex = Store.selectedGrid.position.x

        this.item.patch({
            y: y,
            selectedIndex: selectedVideoIndex,
        })
    }

    private set yPosition(value: number) {
        const animationDuration = 0.5
        this.setSmooth('y', value, { duration: animationDuration })
    }

    private get item() {
        return this.tag('Item') as BaseRow
    }
}
