import LiveRow from './LiveRow'
import PagesRow from './PagesRow'
import PlaylistRow from './PlaylistRow'
import PlaylistsRow from './PlaylistsRow'
import VideosRow from './VideosRow'
import { Channel } from '../../Data/Models'
import { ChannelRow } from '../../ViewModels/ViewModels'

export default function CreateRow(channel: Channel, titleVisible = true): ChannelRow | null {
    const data = { data: channel, titleVisible: titleVisible }

    switch (channel.type) {
        case 'pages':
            return { type: PagesRow, ...data }

        case 'playlist':
            return { type: PlaylistRow, ...data }

        case 'playlists':
            return { type: PlaylistsRow, ...data }

        case 'videos':
            return { type: VideosRow, ...data }

        case 'playmakerLive':
            return { type: LiveRow, ...data }

        default:
            return null
    }
}
