import { Lightning, Template } from '@lightningjs/sdk'

export default class Button extends Lightning.Component {
    private static buttonWidth = 174
    private static buttonHeight = 61

    static _template(): Template {
        const fontSize = 25

        return {
            w: Button.buttonWidth,
            h: Button.buttonHeight,
            texture: Lightning.Tools.getRoundRect(
                Button.buttonWidth,
                Button.buttonHeight,
                0,
                2,
                0xffffffff,
                false,
                0xe6000000
            ),
            Text: {
                w: Button.buttonWidth,
                y: 12,
                text: {
                    text: '',
                    textAlign: 'center',
                    fontSize: fontSize,
                    textColor: 0xffffffff,
                    fontFace: 'FoundersGroteskCondensed-Semibold',
                },
            },
        }
    }

    set buttonText(value: string) {
        this.patch({ Text: { text: value } })
    }

    _focus(): void {
        this.patch({
            texture: Lightning.Tools.getRoundRect(
                Button.buttonWidth,
                Button.buttonHeight,
                0,
                2,
                0xffffffff,
                true,
                0xffffffff
            ),
            Text: { text: { textColor: 0xe6000000 } },
        })
    }

    _unfocus(): void {
        this.patch({
            texture: Lightning.Tools.getRoundRect(
                Button.buttonWidth,
                Button.buttonHeight,
                0,
                2,
                0xffffffff,
                false,
                0xe6000000
            ),
            Text: { text: { textColor: 0xffffffff } },
        })
    }
}
