import { Lightning, Template, States, Log, Router, VideoPlayer } from '@lightningjs/sdk'
import { BrandData, Video } from '../../Data/Models'
import { Column } from 'lightning-ui-components'
import CreateRow from '../../components/rows'
import PlayerUI from '../../PlayerUI'
import { CellSelectedPayload } from '../../ViewModels/ViewModels'
import { PagesCell, PlaylistsCell } from '../../components/cells'
import Store from '../../Store'
import { Channel } from '../../Data/Models'
import Events, { PlayerEvent } from '../../Events'
import Timer from '../../components/Timer'
import SplitPage from '../SplitPage'
import HomePageTop from './HomePageTop'
import PlayerMetrics from '../../Analytics/PlayerMetrics'
import Platform from '../../Platform'
import { clamp } from '../../utils'
import BaseRow from '../../components/rows/BaseRow'

export default class HomePage extends SplitPage<BrandData> {
    private params = {
        hiddenOnLoad: false,
        contentFocusedOnLoad: false,
        skipAutoPlay: false,
        focusOnLastItem: false,
        navigateBack: false,
    }

    private _hideTimer = new Timer(this, 20, () => {
        if (this.active) {
            this._setState('AllHidden')
        }
    })

    protected _contentSettings = {
        enabled: true,
        yOffsetByState: {
            landingFocused: 970,
            contentFocused: 0,
            allHidden: 1100,
        },
    }

    private _toasterFired = false
    private _secondsToShowExitToast = 15

    static _template(): Template {
        return {
            ...super._template(),
            landingComponent: {
                type: HomePageTop,
            },
            contentComponent: {
                y: 20,
                itemSpacing: 69,
                type: Column,
            },
            PlayerUI: {
                type: PlayerUI,
            },
        }
    }

    static _states(): States {
        return [
            class LandingFocused extends this {
                $enter() {
                    const row = CreateRow(Store.contentGrid.channels[0])

                    this.landingComponent.patch({ item: row })
                    this.landingContainer.setSmooth('alpha', 1, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.landingFocused,
                        this.animDuration
                    )

                    this.tag('PlayerUI').setSmooth('alpha', 0)
                    this.widgets.tabmenu.show()
                }
                $exit() {
                    this.landingContainer.setSmooth('alpha', 0, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.allHidden,
                        this.animDuration
                    )

                    this.widgets.tabmenu.hide()
                }
                _getFocused() {
                    return this.landingComponent
                }
                _handleUp() {
                    Router.focusWidget('TabMenu')
                }
                _handleDown() {
                    if (this._contentSettings.enabled) {
                        this._setState('ContentFocused')
                        return true
                    }
                    return false
                }
                _handleBack(): void {
                    Router.focusWidget('TabMenu')
                }
                async $cellSelected(payload: CellSelectedPayload): Promise<void> {
                    this._updateStore(payload)
                    super.$cellSelected(payload)
                }
            },
            class ContentFocused extends this {
                $enter() {
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.contentFocused,
                        this.animDuration
                    )
                    this.contentBackground.setSmooth('alpha', 1, this.animDuration)
                }
                $exit() {
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.allHidden,
                        this.animDuration
                    )
                    this.contentBackground.setSmooth('alpha', 0, this.animDuration)
                }
                _getFocused(): Lightning.Element {
                    return this.contentComponent
                }
                _handleUp() {
                    this._setState('LandingFocused')
                }
                _handleBack(): void {
                    this._setState('LandingFocused')
                }
                async $cellSelected(payload: CellSelectedPayload): Promise<void> {
                    this._updateStore(payload)
                    super.$cellSelected(payload)
                }
            },
            class AllHidden extends this {
                $enter() {
                    this.tag('Containers').setSmooth('alpha', 0, this.animDuration)
                    this.tag('PlayerUI').setSmooth('alpha', 1)
                    this.widgets.tabmenu.hide()
                }
                $exit() {
                    this.tag('Containers').setSmooth('alpha', 1, this.animDuration)
                    this.tag('PlayerUI').setSmooth('alpha', 0)
                }
                _getFocused(): Lightning.Element {
                    return this.tag('PlayerUI')
                }
                _handleBack(): void {
                    if (Store.flags.playingDeeplinkedVideo) return
                    if (!this.params.navigateBack) {
                        const firstRowFocused = Store.contentGrid.position.y === 0
                        this._setState(firstRowFocused ? 'LandingFocused' : 'ContentPeeking')
                    } else {
                        Router.step(-1)
                    }
                }
            },
            class ContentPeeking extends this {
                $enter() {
                    const {
                        x: selectedVideoIndex,
                        y: selectedChannelIndex,
                    } = Store.selectedGrid.position
                    const row = {
                        ...CreateRow(Store.selectedChannel),
                        selectedIndex: selectedVideoIndex,
                    }

                    this.landingComponent.patch({ item: row })
                    this._focusCell({ x: 0, y: selectedChannelIndex + 1 }) // TODO: x: 0 is not exactly accurate with the roku app, but I think we need to revisit this idea of separating the grid into "peek" and "content" sections for making it work good
                    this.landingContainer.setSmooth('alpha', 1, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.landingFocused,
                        this.animDuration
                    )

                    this.tag('PlayerUI').setSmooth('alpha', 0)
                    this.widgets.tabmenu.show()
                }
                $exit() {
                    this.landingContainer.setSmooth('alpha', 0, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.allHidden,
                        this.animDuration
                    )
                    this.widgets.tabmenu.hide()
                }
                _getFocused() {
                    return this.landingComponent
                }
                _handleUp() {
                    const { y: selectedChannelIndex } = Store.selectedGrid.position
                    const peekRowOffset = 1
                    const goingUpOffset = 1
                    const newChannelIndex = Math.max(
                        selectedChannelIndex - peekRowOffset - goingUpOffset,
                        0
                    )
                    const backToTheTop = newChannelIndex === 0
                    if (backToTheTop) {
                        this._focusCell({ y: 0 })
                        this._setState('LandingFocused')
                    } else {
                        this._focusCell({ y: newChannelIndex })
                        this._setState('ContentFocused')
                    }
                }
                _handleDown() {
                    this._setState('ContentFocused')
                }
                _handleBack(): void {
                    this._setState('LandingFocused')
                }
                async $cellSelected(payload: CellSelectedPayload): Promise<void> {
                    super.$cellSelected(payload)
                    Store.selectedGrid.position.x = payload.row.selectedIndex
                }
            },
        ]
    }

    //
    // callbacks
    _init(): void {
        Events.on('$player.playerEvent', this.$playerEvent.bind(this))
        Events.on('$app.cellSelected', this.$cellSelected.bind(this))
    }

    _disable(): void {
        VideoPlayer.pause()
        PlayerMetrics.stop()
    }

    $playerEvent({ type: eventType }: PlayerEvent): void {
        switch (eventType) {
            case 'Error':
            case 'Ended': {
                if (Store.flags.playingDeeplinkedVideo) {
                    Store.flags.playingDeeplinkedVideo = false
                }
                this._playNextVideo()
                break
            }
            case 'TimeUpdate': {
                if (Store.flags.playingDeeplinkedVideo) {
                    this._checkIfExitToastNeeded()
                }
                break
            }
            default:
                break
        }
    }

    _checkIfExitToastNeeded(): void {
        const remainingTime = VideoPlayer.duration - VideoPlayer.currentTime
        if (!this._toasterFired && remainingTime <= this._secondsToShowExitToast) {
            this._toasterFired = true
            Platform.showToaster('ExitMessage.PressExit')
        }
    }

    private _playFirstVideo(): void {
        const playlistChannels = Store.contentPlaylistChannels
        const video = playlistChannels[0].videos[0]
        PlayerMetrics.stop()
        this._selectVideo(video)
        PlayerMetrics.start()
    }

    private _playNextVideo(): void {
        Store.selectNextVideoOrChannel()
        const video = Store.selectedVideo
        if (video) {
            this._selectVideo(video)
        }
    }

    private _selectVideo(video: Video): void {
        Events.emit('$app.videoSelected', video)
    }

    _focus(): void {
        this._hideTimer.start()
    }

    _onDataProvided(): void {
        const channels = this.data.channels
        const rows = channels.map((channel: Channel) => CreateRow(channel))

        const [firstRow, ...restOfRows] = rows

        this.landingComponent.patch({ item: firstRow })
        this.contentComponent.patch({ items: restOfRows })

        if (Store.isReady) {
            Store.contentGrid.channels = channels
        } else {
            Store.contentGrid = {
                channels: channels,
                position: { x: 0, y: 0 },
            }
        }

        if (this.params.hiddenOnLoad) {
            this._setState('AllHidden')
        } else if (this.params.contentFocusedOnLoad) {
            this._setState('ContentFocused')
        } else {
            this._setState('LandingFocused')
        }

        if (!this.params.skipAutoPlay) {
            this._playFirstVideo()
        }

        if (this.params.focusOnLastItem) {
            this._focusCell(Store.contentGrid.position)
        }
    }

    private _focusCell({
        x: rowIndex = this.contentRowIndex,
        y: columnIndex = this.contentColumnIndex,
    }): void {
        if (columnIndex == 0) {
            const landingRow = this.landingComponent as BaseRow
            landingRow.selectedIndex = rowIndex
        } else {
            this.contentColumnIndex = clamp(columnIndex - 1, 0, this.column.items.length - 1)
            this.contentRowIndex = rowIndex
        }
    }

    private _updateStore(payload: CellSelectedPayload) {
        const newState = {
            channels: this.data.channels,
            position: {
                x: payload.row.selectedIndex,
                y: this.channelIndex,
            },
        }

        Store.contentGrid = newState
        Store.selectedGrid = newState
    }

    set contentColumnIndex(value: number) {
        this.column.selectedIndex = value
    }

    get contentColumnIndex(): number {
        return this.column.selectedIndex
    }

    get contentRowIndex(): number {
        return this.column.selected.row.selectedIndex
    }

    set contentRowIndex(value: number) {
        this.column.selected.row.selectedIndex = value
    }

    private get channelIndex() {
        return this._getState() == 'LandingFocused' ? 0 : this.column.selectedIndex + 1
    }

    //
    // events
    async $cellSelected(payload: CellSelectedPayload): Promise<void> {
        Log.info('HomePage $cellSelected >', payload)
        const cellData = payload.cell.data

        if ('videoUrl' in cellData) {
            PlayerMetrics.stop()
            const video = cellData
            this._selectVideo(video)
            PlayerMetrics.start()
        } else if ('title' in cellData) {
            const uri = encodeURIComponent(cellData.id)

            if (payload.cell instanceof PagesCell) {
                Router.navigate('show/' + uri)
            } else if (payload.cell instanceof PlaylistsCell) {
                Router.navigate('playlist/' + uri)
            }
        }

        this._setState('AllHidden')
        Log.info('< $cellSelected')
    }

    //
    // properties
    private get column() {
        return this.contentComponent as Column
    }

    //
    // input
    _handleBack(): void {
        // prevent the router from consuming this event
    }

    _captureKey(): boolean {
        this._hideTimer.start()
        return false
    }
}
