import BasePlatform, { DeviceType } from './BasePlatform'
import { keyMapping, ToastMessageIdentifier } from './PlatformInterface'
import { BadgerAccessError } from '../Errors'

export default class X1 extends BasePlatform {
    private async _isBadgerActive(): Promise<void> {
        return new Promise(resolve => {
            if (window.$badger && window.$badger.active()) {
                resolve()
            } else {
                document.addEventListener('onMoneyBadgerReady', () => {
                    resolve()
                })
            }
        })
    }

    private _setStaticDeviceCapabilities(deviceCapabilities: $badger.X1DeviceCapabilities) {
        const screenDimensions = deviceCapabilities.nativeDimensions
        this._screenWidth = screenDimensions[0]
        this._screenHeight = screenDimensions[1]
    }

    public async init(): Promise<void> {
        if (!window.$badger) {
            const X1JavascriptAPI = document.createElement('script')
            X1JavascriptAPI.setAttribute(
                'src',
                'https://cdn.thor.comcast.com/sdk/money-badger.min.js'
            )
            document.head.appendChild(X1JavascriptAPI)
        }
        await this._isBadgerActive()
        window.$badger
            .deviceCapabilities()
            .success(deviceCaps => {
                this._setStaticDeviceCapabilities(deviceCaps)
                return
            })
            .failure(() => {
                throw new BadgerAccessError()
            })
    }

    public async getHDMIConnectedStatus(): Promise<boolean> {
        return new Promise((resolve, reject) => {
            window.$badger
                .deviceCapabilities()
                .success(deviceCaps => {
                    resolve(deviceCaps.hdcp.connected)
                })
                .failure(() => {
                    reject(false)
                    throw new BadgerAccessError()
                })
        })
    }

    public async getCloseCaptioningEnabled(): Promise<boolean> {
        return new Promise((resolve, reject) => {
            window.$badger
                .userPreferences()
                .closedCaptioning()
                .success(ccSettings => {
                    resolve(ccSettings.enabled)
                })
                .failure(() => {
                    reject(false)
                })
        })
    }

    public shutdown(): void {
        window.$badger.shutdown()
    }

    public errorMetricsHandler(
        message: string,
        visible: boolean,
        code: string,
        args: Record<string, unknown> = {}
    ): void {
        window.$badger.errorMetricsHandler(message, visible, code, args)
    }

    public dismissLoadingScreen(): void {
        window.$badger.dismissLoadingScreen()
    }

    public showToaster(messageIdentifier: ToastMessageIdentifier): void {
        window.$badger.showToaster(messageIdentifier)
    }

    public get keyMapping(): keyMapping {
        return {
            179: 'MediaPlayPause',
            228: 'MediaFastForward',
            227: 'MediaRewind',
        }
    }

    public get deviceType(): DeviceType {
        return 'X1'
    }

    public appActionMetricsHandler(action: string, args: Record<string, unknown> = {}): void {
        window.$badger.appActionMetricsHandler(action, args)
    }
}
