import { Lightning, Template } from '@lightningjs/sdk'
import Section from './Section'

export default class QRSection extends Section {
    static _template(): Template {
        const menuWidth = 400
        const titleMargin = 120
        const infoTextFontSize = 34
        const infoColor = 0x66ffffff

        return {
            Title: {
                x: menuWidth - titleMargin,
                h: 45,
                mountX: 1,
                text: {
                    text: '',
                    fontSize: 30,
                    textAlign: 'right',
                    fontFace: 'FoundersGroteskCondensed-Regular',
                },
            },
            Content: {
                x: menuWidth,
                alpha: 0,
                Image: {
                    x: 156,
                    y: 383,
                },
                Texts: {
                    x: 451,
                    y: 447,
                    Info: {
                        text: {
                            text: '',
                            fontSize: infoTextFontSize,
                            textColor: infoColor,
                            fontFace: 'FoundersGroteskCondensed-Regular',
                        },
                    },
                    Link: {
                        y: 97,
                        mountY: 0.5,
                        text: {
                            text: '',
                            fontSize: 60,
                            textColor: 0xffffffff,
                            fontFace: 'FoundersGroteskCondensed-Semibold',
                        },
                    },
                    SubInfo: {
                        y: 163,
                        text: {
                            text: '',
                            fontSize: infoTextFontSize,
                            textColor: infoColor,
                            fontFace: 'FoundersGroteskCondensed-Regular',
                        },
                    },
                },
            },
        }
    }

    set focused(value: boolean) {
        this.patch({
            Title: {
                smooth: { alpha: value ? 1 : 0.4 },
            },
            Content: {
                smooth: { alpha: value ? 1 : 0 },
            },
        })
    }

    get title(): Lightning.Element {
        return this.tag('Title')
    }

    set title(value: Lightning.Element) {
        this.title.patch({ text: value })
    }

    set image(src: string) {
        this.tag('Content.Image').src = src
    }

    set info(text: string | Record<string, unknown>) {
        this.tag('Content.Texts.Info').patch({ text: text })
    }

    set link(text: string | Record<string, unknown>) {
        this.tag('Content.Texts.Link').patch({ text: text })
    }

    set subinfo(text: string | Record<string, unknown>) {
        this.tag('Content.Texts.SubInfo').patch({ text: text })
    }
}
