import { FetchError } from '../Errors'
import { BrandData, Resource, Playlist, Show, Video } from './Models'
import Config from '../Config'
import analytics from '../Analytics/Analytics'

export default class Data {
    public current: BrandData | null = null

    private _config: Config
    private _lastModified: string | null = null

    constructor(config: Config) {
        this._config = config
    }

    async fetchBrandData(params: { for: string }): Promise<BrandData> {
        const path = this._config.brands[params.for]
        const url = this._config.urls.apiBase + path

        const result = await fetch(url, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
            },
            mode: 'cors',
        })

        if (result.ok) {
            const root = await result.json()
            this.current = root
            this._lastModified = result.headers.get('last-modified')

            analytics.contentLoaded('brand', result.url)
            return root
        } else {
            throw new FetchError(result.statusText, result.status, result.url)
        }
    }

    async fetchShowFromCard(cardId: string): Promise<Show> {
        const show = await this.fetchResource(cardId)
        return show as Show
    }

    async fetchPlaylistFromCard(cardId: string): Promise<Playlist> {
        const playlist = await this.fetchResource(cardId)
        return playlist as Playlist
    }

    async fetchVideoFromId(videoId: string): Promise<Video> {
        const video = await this.fetchResource(videoId)
        return video as Video
    }

    async fetchResource(resourceId: string): Promise<Resource> {
        const url = this._config.urls.apiBase + resourceId

        const result = await fetch(url, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
            },
            mode: 'cors',
        })

        if (result.ok) {
            const json = await result.json()
            return json as Resource
        } else {
            throw new FetchError(result.statusText, result.status, result.url)
        }
    }

    async fetchSearchQuery(query: string): Promise<Video[]> {
        const url = this._config.urls.apiBase + 'ott/search?q=' + query

        const result = await fetch(url, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
            },
            mode: 'cors',
        })

        if (result.ok) {
            const json = await result.json()
            return json.videos as Video[]
        } else {
            return []
        }
    }
}
