import { Template } from '@lightningjs/sdk'
import BaseCell from './BaseCell'
import { NBCDAimsUrl } from '../../utils'

export default class VideosCell extends BaseCell {
    private static width = 812
    private static height = 311

    static _template(): Template {
        return {
            ...VideosCell._getBaseTemplate(VideosCell.width, VideosCell.height),
        }
    }

    protected _render(): void {
        if ('tease' in this.data) {
            this.image.src = NBCDAimsUrl(this.data.tease, VideosCell.width, VideosCell.height)
        }
    }

    protected set _focused(value: boolean) {
        this.image.patch({
            smooth: {
                scale: value ? 1.053 : 1,
            },
            shader: {
                strokeColor: value ? 0xffffffff : 0xff393939,
            },
        })
    }
}
