import { Lightning, Router, States, Template, Utils } from '@lightningjs/sdk'
import BaseRow from '../../components/rows/BaseRow'

export default class CardPageTop extends Lightning.Component {
    static _template(): Template {
        return {
            Title: {
                x: 127,
                y: 92,
                text: {
                    fontFace: 'FoundersGroteskCondensed-Semibold',
                    fontSize: 88,
                    cutSy: 32,
                },
            },
            Subtitle: {
                x: 127,
                y: 170,
                text: {
                    fontFace: 'FoundersGroteskCondensed-Light',
                    fontSize: 44,
                    cutSy: 16,
                },
            },
            BackButton: {
                x: 127,
                y: 380,
                src: Utils.asset('images/back_button.png'),
            },
            Item: {},
        }
    }

    static _states(): States {
        return [
            class BackButton extends this {
                $enter() {
                    this.backButton.setSmooth('scale', 1.2)
                }
                $exit() {
                    this.backButton.setSmooth('scale', 1)
                }
                _getFocused() {
                    return this
                }
                _handleEnter() {
                    Router.step(-1)
                }
                _handleDown() {
                    this._setState('Item')
                }
            },
            class Item extends this {
                _handleUp() {
                    this._setState('BackButton')
                }
                _getFocused() {
                    return this.item
                }
            },
        ]
    }

    //
    // callbacks
    _enable(): void {
        this._setState('Item')
    }

    //
    // properties
    private get backButton(): Lightning.Element {
        return this.tag('BackButton')
    }

    set title(value: string) {
        this.patch({
            Title: { text: { text: value.toUpperCase() } },
        })
    }

    set subtitle(value: string) {
        this.patch({
            Subtitle: { text: { text: value || '' } },
        })
    }

    get item(): BaseRow {
        return this.tag('Item') as BaseRow
    }

    set item(value: BaseRow) {
        this.patch({ Item: undefined })
        this.patch({ Item: value })
        this.item.y = 1080 - this.item.h - 150
    }
}
