import { VideoPlayer, Log, Registry, Settings } from '@lightningjs/sdk'
import Hls from 'hls.js'
import PLAYER_EVENTS_MAPPING from './playerEvents'
import { PlayerEvent } from '../Events'
import Platform from '../Platform'

interface PlayerConfig {
    application: string
    platform: string
    requestorId: string
}

type InnerPlayerConfig = PlayerConfig & {
    pidNotificationsUrl: string
    pidNotificationsPollingInterval: number
}

function setVideoProtocol(url: string): string {
    if (document.location.protocol == 'https:') return url.replace('http:', 'https:')
    return url
}

export default class HLSPlayer implements EventListenerObject {
    video: HTMLMediaElement = VideoPlayer._videoEl
    config: InnerPlayerConfig
    pid: string | null = null
    monitorTimer: number | null = null
    onPlayerEvent: (ev: PlayerEvent) => void

    private _hls?: Hls

    constructor(config: PlayerConfig, onPlayerEvent: (ev: PlayerEvent) => void) {
        this.onPlayerEvent = onPlayerEvent
        this.config = {
            ...config,
            // TD: get from appconfig
            pidNotificationsUrl: 'http://stream.nbcsports.com/data/lowttl/notifications_{PID}.json',
            pidNotificationsPollingInterval: 20000,
        }
        this.registerEventListeners(this.video)
        VideoPlayer.isPlaying = false

        if (Hls.isSupported()) {
            VideoPlayer.loader(this._hlsLoader.bind(this))
            VideoPlayer.unloader(this._hlsUnloader.bind(this))
        }
    }

    private async _hlsLoader(
        url: string,
        videoEl: HTMLMediaElement,
        { startPosition = -1 }
    ): Promise<void> {
        if (!this._hls) {
            this._hls = new Hls({
                autoStartLoad: false,
                debug: Settings.get('app', 'debug'),
            })

            // HLS.js swallows native error events
            this._hls.on(
                Hls.Events.ERROR,
                (event: string, data: { fatal: boolean; details: string }) => {
                    // bufferAppendError is fired when the video loads for some reason
                    if (data.fatal && data.details !== 'bufferAppendError') {
                        this.onPlayerEvent({
                            type: 'Error',
                            timestamp: new Date().getTime(),
                        })
                    }
                }
            )

            this._hls.on(Hls.Events.MANIFEST_PARSED, () => {
                if (this._hls) {
                    this._hls.startLoad(startPosition)
                }
                videoEl.play()
            })

            this._hls.subtitleDisplay = true

            videoEl.textTracks.onaddtrack = async () => {
                this.ccEnabled = await Platform.getCloseCaptioningEnabled()
            }

            Platform.registerClosedCaptionsStatusListener((ccEnabled: boolean) => {
                this.ccEnabled = ccEnabled
            })

            // hls.on(Hls.Events.SUBTITLE_TRACKS_UPDATED, () => {
            //     Log.info('SUBTITLE_TRACKS_UPDATED', hls.subtitleTracks)
            // })
        }

        Log.info('hls v', Hls.version, this._hls.config.enableCEA708Captions)

        this._hls.stopLoad()
        this._hls.attachMedia(videoEl)
        this._hls.loadSource(url)
    }

    private async _hlsUnloader(videoEl: HTMLMediaElement): Promise<void> {
        if (this._hls) {
            this._hls.destroy()
        }

        videoEl.removeAttribute('src')
        videoEl.load()
    }

    registerEventListeners(videoEl: HTMLMediaElement): void {
        for (const event of PLAYER_EVENTS_MAPPING) {
            Registry.addEventListener(videoEl, event[0], this)
        }
    }

    handleEvent(event: Event): void {
        const mappedEvent = PLAYER_EVENTS_MAPPING.get(event.type)
        if (mappedEvent) {
            this.onPlayerEvent({
                type: mappedEvent,
                timestamp: event.timeStamp,
            })
            // Needed as we can't rely on VideoPlayer's internal state,
            // since the lng's listeners don't get attached if we use hls.js
            switch (mappedEvent) {
                case 'Play':
                case 'Playing':
                    VideoPlayer.isPlaying = true
                    break
                case 'Pause':
                case 'LoadStart':
                case 'Ended':
                case 'Error':
                case 'Abort':
                    VideoPlayer.isPlaying = false
                    break
                default:
                    break
            }
        }
    }

    deregisterEventListeners(videoEl: HTMLMediaElement): void {
        for (const event of PLAYER_EVENTS_MAPPING) {
            Registry.removeEventListener(videoEl, event[0], this)
        }
    }

    async playWithPid(pid: string, startPosition?: number): Promise<void> {
        Log.info('play pid:', pid)

        this.stopPidMonitor()
        const result = await fetch(`https://api.leap.nbcsports.com/pid/NBCNews/${pid}/v4/roku`, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
            },
        })

        if (result.ok) {
            const root = await result.json()

            let url = root.videoSources[0].cdnSources.primary[0].sourceUrl

            const auth = root.auth
            if (auth.cdnToken && auth.authenticationType == 'unauth') {
                url = await this.fetchUnauthToken(url, pid)
            }

            this.playWithUrl(url, startPosition)
            this.startPidMonitor(pid)
        }
    }

    playWithUrl(url: string, startPosition?: number): void {
        Log.info('playWithUrl >', url)

        VideoPlayer.open(setVideoProtocol(url), { startPosition })

        Log.info('< playWithUrl')
    }

    // get unauth token
    async fetchUnauthToken(url: string, pid: string): Promise<string | null> {
        const json = {
            authenticationType: 'unauth',
            application: this.config.application,
            cdn: 'akamai',
            pid: pid,
            platform: this.config.platform,
            requestorId: this.config.requestorId,
            resourceId: '',
            token: '',
            url: url,
            version: 'v1',
        }

        const result = await fetch('https://tokens.playmakerservices.com/cdn', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(json),
        })

        //Log.info("token", r)

        if (result.ok) {
            const root = await result.json()
            return root.akamai[0].tokenizedUrl
        }

        // TD: throw error
        return null
    }

    startPidMonitor(pid: string): void {
        this.pid = pid
        this.monitorTimer = Registry.setInterval(async () => {
            const result = await fetch(this.config.pidNotificationsUrl.replace('{pid}', pid), {
                method: 'GET',
                headers: {
                    Accept: 'application/json',
                },
            })

            if (result.ok) {
                const root = await result.json()
                if (root.notifications != null && root.notifications[0] != null) {
                    const redirectPid = this.pidFromNotification(root.notifications[0])
                    if (redirectPid != null && redirectPid != this.pid) {
                        this.playWithPid(redirectPid)
                    }
                }
            }
        }, this.config.pidNotificationsPollingInterval)
    }

    pidFromNotification(notification: { action: string; pid: string }): string | null {
        if (notification.action == '3') {
            return notification.pid
        }
        return null
    }

    stopPidMonitor(): void {
        if (this.monitorTimer) {
            clearInterval(this.monitorTimer)
            this.monitorTimer = null
            this.pid = null
        }
    }

    _detach(): void {
        this.deregisterEventListeners(this.video)
    }

    seek(value: number): void {
        VideoPlayer.seek(value)
    }

    set ccEnabled(enabled: boolean) {
        if (this.video.textTracks[0]) {
            this.video.textTracks[0].mode = enabled ? 'showing' : 'disabled'
        }
    }

    get ccEnabled(): boolean {
        return this.video.textTracks[0] && this.video.textTracks[0].mode === 'showing'
    }
}
