import { Lightning, States, Template } from '@lightningjs/sdk'
import BasePage from './BasePage'

export default abstract class SplitPage<T> extends BasePage<T> {
    protected animDuration = { duration: 0.3 }

    protected _contentSettings = {
        enabled: true,
        yOffsetByState: {
            landingFocused: 950,
            contentFocused: 0,
            allHidden: 1100,
        },
    }

    static _template(): Template {
        const w = 1920
        const h = 1080

        return {
            w,
            h,
            // TODO Make this work with class properties only
            _contentSettings: {
                enabled: null,
                yOffsetByState: {
                    landingFocused: null,
                    contentFocused: null,
                    allHidden: null,
                },
            },
            Containers: {
                LandingContainer: {
                    w,
                    h,
                    LandingComponent: {},
                },
                ContentBackground: {
                    w,
                    h,
                    alpha: 0,
                    rect: true,
                    color: 0xed000000,
                },
                ContentContainer: {
                    w,
                    h,
                    clipping: true,
                    ContentComponent: {},
                },
            },
        }
    }

    static _states(): States {
        return [
            class LandingFocused<T> extends this<T> {
                $enter() {
                    this.landingContainer.setSmooth('alpha', 1, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.landingFocused,
                        this.animDuration
                    )
                }
                $exit() {
                    this.landingContainer.setSmooth('alpha', 0, this.animDuration)
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.allHidden,
                        this.animDuration
                    )
                }
                _getFocused() {
                    return this.landingComponent
                }
                _handleDown() {
                    if (this._contentSettings.enabled) {
                        this._setState('ContentFocused')
                        return true
                    }
                    return false
                }
            },
            class ContentFocused<T> extends this<T> {
                $enter() {
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.contentFocused,
                        this.animDuration
                    )
                    this.contentBackground.setSmooth('alpha', 1, this.animDuration)
                }
                $exit() {
                    this.contentContainer.setSmooth(
                        'y',
                        this._contentSettings.yOffsetByState.allHidden,
                        this.animDuration
                    )
                    this.contentBackground.setSmooth('alpha', 0, this.animDuration)
                }
                _getFocused() {
                    return this.contentComponent
                }
                _handleUp() {
                    this._setState('LandingFocused')
                }
                _handleBack() {
                    this._setState('LandingFocused')
                }
            },
            class AllHidden<T> extends this<T> {
                $enter() {
                    this.tag('Containers').setSmooth('alpha', 0, this.animDuration)
                }
                $exit() {
                    this.tag('Containers').setSmooth('alpha', 1, this.animDuration)
                }
                _handleBack() {
                    this._setState('LandingFocused')
                }
            },
        ]
    }

    //
    // component's events
    _init(): void {
        this._setState('LandingFocused')
    }

    //
    // properties
    protected get landingContainer(): Lightning.Element {
        return this.tag('LandingContainer')
    }

    protected get contentContainer(): Lightning.Element {
        return this.tag('ContentContainer')
    }

    protected get contentBackground(): Lightning.Element {
        return this.tag('ContentBackground')
    }

    get landingComponent(): Lightning.Element {
        return this.tag('LandingComponent')
    }

    set landingComponent(value: Lightning.Element) {
        this.landingContainer.patch({ LandingComponent: undefined })
        this.landingContainer.patch({ LandingComponent: value })
    }

    get contentComponent(): Lightning.Element {
        return this.tag('ContentComponent')
    }

    set contentComponent(value: Lightning.Element) {
        this.contentContainer.patch({ ContentComponent: undefined })
        this.contentContainer.patch({
            ContentComponent: value,
            y: this._contentSettings.yOffsetByState.landingFocused,
        })
    }
}
