export enum BrandName {
    nbcnews = 'nbcnews',
    msnbc = 'msnbc',
    today = 'today',
}

export type Channel = Playlist | Cards

export interface Position {
    x: number
    y: number
}

export interface CellGrid {
    id?: string
    channels: Channel[]
    position: Position
}

export interface BrandData {
    id: string
    type: string
    title: string
    brand: BrandName
    channels: Channel[]
}

export interface Cards {
    id: string
    type: 'pages' | 'playlists'
    title: string
    cards: Card[]
}

export interface Card {
    id: string
    title: string
    image: string //URL
    tagline: string
}

export interface Playlist {
    id: string
    title: string
    type: 'playlist' | 'videos' | 'playmakerLive' | 'tveEpisodes'
    isLive: boolean
    image?: string //URL
    description?: string
    duration?: string // hh.mm.ss | "live"
    videos: Video[]
}

export interface Video {
    id: string
    guid: string // not a GUID
    brand: string
    videoType: string
    url: string //URL
    headline: string
    published: Date
    tease: string //URL
    duration: string
    summary?: string
    preview?: string | null
    videoUrl?: string | null
    //bif: URL // only roku
    tracking?: TrackingParameters
    isAuth?: boolean
}

export interface TrackingParameters {
    [index: string]: string
}

export interface Show {
    id: string
    type: 'show'
    brand: string
    available: string
    episodes?: Playlist
    highlights: Playlist
    segments?: Playlist[]
    tagline: string
    thumb: string // URL
    title: string
}

export interface CardPageData {
    cardId: string
    title: string
    subtitle?: string
    backgroundUrl?: string
    activeBrand?: BrandName
    rows: Playlist[]
}

export type Resource = Playlist | Show | Video

export interface LayoutKeyData {
    label: string
    characters: string[]
}

export interface FunctionKeyData {
    label: string
    eventName: string
    src?: string
}

export type InputFieldMask = (value: string) => string
