import { Router, Settings } from '@lightningjs/sdk'
import Config from './Config'
import Data from './Data/Data'
import { BrandName } from './Data/Models'
import Events from './Events'
import {
    CardPage,
    ErrorPage,
    HomePage,
    SearchPage,
    SettingsPage,
    SplashPage,
    VideoPage,
} from './Pages'
import Platform from './Platform'
import Store, { QsParams } from './Store'

let AppData: Data

// Needed for now when deeplinking to launchpoint with the naked url + params
// eg. "lightning.nbcnews.com?launchpoint=section&sectionName=settings"
function getQsFromURL(): QsParams {
    if (window.location.search) {
        const urlParams = new URLSearchParams(window.location.search)
        return Array.from(urlParams).reduce((o, i) => ({ ...o, [i[0]]: i[1] }), {})
    } else {
        return {}
    }
}

function getCuratorEnv(qs: QsParams): string {
    if (Settings.get('app', 'debug')) {
        return qs.curator || 'dev'
    } else {
        return 'prod'
    }
}

export default {
    root: 'splash',
    boot: async (_qs: QsParams | false): Promise<void> => {
        const qs = _qs || getQsFromURL()
        Store.qs = qs
        const config: Config = new Config(getCuratorEnv(qs))
        await config.fetch()
        await Platform.init()
        AppData = new Data(config)
        Platform.dismissLoadingScreen()
    },
    // TODO: A future version of lng will let us grab the hash inside the TabMenu widget
    // with `_onActivated(page), page.hash`. With that, we can remove this hook,
    // as well as the `$app.firstRoute` event
    beforeEachRoute: async (from: Router.From, to: Router.To): Promise<string | boolean> => {
        if (from === undefined) {
            Events.emit('$app.firstRoute', to.hash)
        }
        return true
    },
    routes: [
        {
            path: 'splash',
            component: SplashPage,
            before: async (page: SplashPage): Promise<void> => {
                page.data = AppData
            },
            options: {
                preventStorage: true,
            },
        },
        {
            path: 'brand/:brand',
            component: HomePage,
            widgets: ['TabMenu', 'ExitModal'],
            before: async (page: HomePage, { brand }: { brand: string }): Promise<void> => {
                const data = await AppData.fetchBrandData({ for: brand })
                // TODO: Put behind feature flag
                // data.channels = data.channels.map(ch => {
                //     if (ch.type === 'playmakerLive') {
                //         ch.videos = ch.videos.filter(vid => !vid.isAuth)
                //     }

                //     return ch
                // })
                data.channels = data.channels.filter(ch => ch.type !== 'playmakerLive')

                page.data = data
            },
        },
        {
            path: 'search',
            component: SearchPage,
            widgets: ['TabMenu', 'ExitModal'],
            before: async (page: SearchPage): Promise<void> => {
                page.fetchSearchQuery = async (query: string) =>
                    await AppData.fetchSearchQuery(query)
            },
        },
        {
            path: 'settings',
            component: SettingsPage,
            widgets: ['TabMenu', 'ExitModal'],
        },
        {
            path: 'show/:cardId',
            component: CardPage,
            before: async (page: CardPage, { cardId }: { cardId: string }): Promise<void> => {
                const data = await AppData.fetchShowFromCard(decodeURIComponent(cardId))
                const rows = []

                if (data.episodes && data.episodes.type !== 'tveEpisodes') {
                    rows.push(data.episodes)
                }

                rows.push(data.highlights)

                if (data.segments) {
                    rows.push(...data.segments)
                }

                page.data = {
                    cardId: cardId,
                    title: data.title,
                    subtitle: data.available,
                    backgroundUrl: data.thumb,
                    activeBrand: AppData.current ? AppData.current.brand : undefined,
                    rows: rows,
                }
            },
        },
        {
            path: 'playlist/:cardId',
            component: CardPage,
            before: async (page: CardPage, { cardId }: { cardId: string }): Promise<void> => {
                const data = await AppData.fetchPlaylistFromCard(decodeURIComponent(cardId))

                page.data = {
                    cardId: cardId,
                    title: data.title,
                    backgroundUrl: data.image,
                    activeBrand: AppData.current ? AppData.current.brand : undefined,
                    rows: [data],
                }
            },
        },
        {
            path: 'video/:videoId',
            component: VideoPage,
            before: async (page: VideoPage, { videoId }: { videoId: string }): Promise<void> => {
                page.data = await AppData.fetchVideoFromId(decodeURIComponent(videoId))
            },
            options: {
                preventStorage: true,
            },
        },
        {
            path: 'tab/:tabId',
            beforeNavigate: async (from: Router.From, to: Router.To): Promise<string | boolean> => {
                switch (to.hash) {
                    case 'tab/settings':
                        return 'settings'
                    case 'tab/search':
                        return 'search'
                    default:
                        return 'brand/' + BrandName.nbcnews
                }
            },
            options: {
                preventStorage: true,
            },
        },
        {
            path: '!',
            component: ErrorPage,
        },
    ],
}
