import { Lightning, Template } from '@lightningjs/sdk'
import { Video, Card } from '../../Data/Models'
import { CellDataError } from '../../Errors'
import NowPlayingIndicator from '../NowPlayingIndicator'

export default abstract class BaseCell extends Lightning.Component {
    private _data?: Video | Card
    private _nowPlaying = false

    protected static _getBaseTemplate(w: number, h: number): Template {
        const shadowSize = 25
        const shadowBlur = 20
        return {
            w,
            h,
            Shadow: {
                x: w / 2,
                y: h / 2,
                mount: 0.5,
                color: 0x66000000,
                texture: Lightning.Tools.getShadowRect(
                    w + shadowSize * 3,
                    h + shadowSize * 2,
                    10,
                    shadowBlur
                ),
            },
            Image: {
                w,
                h,
                shader: {
                    type: Lightning.shaders.RoundedRectangle,
                    radius: 10,
                    stroke: 3,
                },
                NowPlayingIndicator: {
                    type: NowPlayingIndicator,
                    alpha: 0,
                    x: w - NowPlayingIndicator.width - 12,
                    y: 12,
                },
            },
        }
    }

    //
    // override
    protected abstract _render(): void

    //
    // callbacks
    private _init() {
        this._focused = false
    }

    private _focus(): void {
        this._focused = true
    }

    private _unfocus(): void {
        this._focused = false
    }

    _getFocused(): Lightning.Component {
        return this
    }

    //
    // properties
    protected set _focused(value: boolean) {
        this.patch({
            Shadow: {
                smooth: { alpha: value },
            },
            Image: {
                smooth: {
                    scale: value ? 1.1 : 1,
                },
                shader: {
                    strokeColor: value ? 0xffffffff : 0xff393939,
                },
            },
        })
    }

    protected get image(): Lightning.Element {
        return this.tag('Image')
    }

    set data(value: Video | Card) {
        this._data = value
        this._render()
    }

    get data(): Video | Card {
        if (this._data) {
            return this._data
        } else {
            throw new CellDataError()
        }
    }

    set nowPlaying(value: boolean) {
        if (value !== this._nowPlaying) {
            this._nowPlaying = value
            this.tag('NowPlayingIndicator').setSmooth('alpha', value ? 1 : 0)
        }
    }

    get nowPlaying(): boolean {
        return this._nowPlaying
    }
}
